import CustomTextField from "app/shared-components/custom-text-field/CustomTextField";
import CustomFormDialog from "app/shared-components/custom-form-dialog/CustomFormDialog";
import { useTranslation } from "react-i18next";
import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { IRejectionReasons } from "../RejectionReasonsModel";
import { z } from "zod";
import { EMPTY_ERROR_MESSAGE } from "app/constants/FormErrors";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import CustomLoadingScreen from "app/shared-components/custom-loading-screen/CustomLoadingScreen";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import { useGetOneRejectionReasonQuery, usePostRejectionReasonMutation, usePutRejectionReasonMutation } from "../RejectionReasonsApi";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";

type RejectionReasonsDialogFormProps = {
	add?: boolean;
	rejectionReasonId?: string;
};

const RejectionReasonsDialogForm = (props: RejectionReasonsDialogFormProps) => {
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const constantTranslation = useTranslation(CONSTANTS);
	const dispatch = useDispatch();
	const { add, rejectionReasonId } = props;
	const [skip, setSkip] = useState(true);
	const [loading, setLoading] = useState(false);
	const getRejectionReasonById = useGetOneRejectionReasonQuery(rejectionReasonId, { refetchOnMountOrArgChange: true, skip });
	const [postRejectionReason, postRejectionReasonResult] = usePostRejectionReasonMutation();
	const [putRejectionReason, putRejectionReasonResult] = usePutRejectionReasonMutation();

	useEffect(() => {
		if (rejectionReasonId) {
			setSkip(false);
		}

		return () => setSkip(true);
	}, [rejectionReasonId]);

	const defaultValues: IRejectionReasons = {
		id: undefined,
		businessId: 1,
		name: ""
	};

	const schema = z.object({
		id: z.number().optional(),
		businessId: z.number().optional(),
		name: z
			.string()
			.min(1, `${settingsTranslation.t("BUSINESS_PHASE")} ${constantTranslation.t("NAME")} ${constantTranslation.t(EMPTY_ERROR_MESSAGE)}`)
			.max(256)
	});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultValues,
		resolver: zodResolver(schema)
	});

	const { handleSubmit, control, formState, setValue, reset } = methods;
	const { isValid, errors, isDirty } = formState;

	useEffect(() => {
		const { data, isSuccess, isLoading, isError, error } = getRejectionReasonById;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			Object.keys(data).forEach((key: any) => setValue(key, data[key]));
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("REJECTION_REASONS")}`, "error")));
		}

		return () => Object.keys(defaultValues).forEach((key: any) => setValue(key, defaultValues[key]));
	}, [getRejectionReasonById]);

	const submitForm = (data: IRejectionReasons) => {
		if (add) {
			postRejectionReason(data);
		} else {
			putRejectionReason(data);
		}
		reset(data);
	};

	useEffect(() => {
		const { isError, isLoading, isSuccess, data, error } = postRejectionReasonResult;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("REJECTION_REASONS")} ${constantTranslation.t("ADD_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ADD_ERROR")} ${settingsTranslation.t("REJECTION_REASONS")}`, "error")));
		}
	}, [postRejectionReasonResult]);

	useEffect(() => {
		const { isError, isLoading, isSuccess, data, error } = putRejectionReasonResult;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("REJECTION_REASONS")} ${constantTranslation.t("SAVE_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("SAVE_ERROR")} ${settingsTranslation.t("REJECTION_REASONS")}`, "error")));
		}
	}, [putRejectionReasonResult]);

	return (
		<CustomFormDialog
			isValid={isValid}
			isDirty={isDirty}
			actions={[{ title: constantTranslation.t("SAVE"), onClick: handleSubmit((data) => submitForm(data)) }]}
			handleSubmit={handleSubmit((data) => submitForm(data))}
			title={add ? `${constantTranslation.t("ADD")} ${settingsTranslation.t("REJECTION_REASONS")}` : `${constantTranslation.t("EDIT")} ${settingsTranslation.t("REJECTION_REASONS")}`}
		>
			<CustomTextField
				control={control}
				name="name"
				label={constantTranslation.t("NAME")}
				errors={errors}
				required
				InputLabelProps={{
					shrink: true
				}}
				dialog="true"
				placeholder={constantTranslation.t("NAME")}
			/>
			<CustomLoadingScreen isLoading={loading} />
		</CustomFormDialog>
	);
};

export default RejectionReasonsDialogForm;
