import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { z } from "zod";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import CustomFormDialog from "app/shared-components/custom-form-dialog/CustomFormDialog";
import CustomDatePickerField from "app/shared-components/custom-date-picker-field/CustomDatePickerField";
import { ISlotReleaseSchedule } from "../SlotReleaseScheduleModel";
import { useGetOneSlotReleaseScheduleQuery, usePostSlotReleaseScheduleMutation, usePutSlotReleaseScheduleMutation } from "../SlotReleaseScheduleApi";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import moment from "moment";

type HighDemandDialogFormProps = {
	add?: boolean;
	slotReleaseScheduleId?: string;
};

const SlotReleaseScheduleDialogForm = (props: HighDemandDialogFormProps) => {
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const constantTranslation = useTranslation(CONSTANTS);
	const dispatch = useDispatch();
	const { add, slotReleaseScheduleId } = props;
	const [skip, setSkip] = useState(true);
	const [loading, setLoading] = useState(false);
	const getSlotReleaseScheduleById = useGetOneSlotReleaseScheduleQuery(slotReleaseScheduleId, { refetchOnMountOrArgChange: true, skip });
	const [postSlotReleaseSchedule, postSlotReleaseScheduleResult] = usePostSlotReleaseScheduleMutation();
	const [putSlotReleaseSchedule, putSlotReleaseScheduleResult] = usePutSlotReleaseScheduleMutation();

	useEffect(() => {
		if (slotReleaseScheduleId) {
			setSkip(false);
		}

		return () => setSkip(true);
	}, [slotReleaseScheduleId]);

	const defaultValues: ISlotReleaseSchedule = {
		id: undefined,
		businessId: 1,
		start: new Date(),
		end: new Date(),
		periodStart: new Date(),
		periodEnd: new Date()
	};

	const schema = z.object({
		id: z.number().optional(),
		businessId: z.number().optional(),
		start: z.date(),
		end: z.date(),
		periodStart: z.date(),
		periodEnd: z.date()
	});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultValues,
		resolver: zodResolver(schema)
	});

	const { handleSubmit, control, formState, setValue, reset } = methods;
	const { isValid, errors, isDirty } = formState;

	useEffect(() => {
		const { data, isSuccess, isLoading, isError, error } = getSlotReleaseScheduleById;

		setLoading(isLoading);

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			Object.keys(data).forEach((key: any) => {
				if (typeof data[key] === "string" && moment(data[key]).isValid()) {
					setValue(key, new Date(data[key]));
				} else {
					setValue(key, data[key]);
				}
			});
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("BOOKINGS_SCHEDULE")}`, "error")));
		}

		return () => Object.keys(defaultValues).forEach((key: any) => setValue(key, defaultValues[key]));
	}, [getSlotReleaseScheduleById]);

	const submitForm = (data: ISlotReleaseSchedule) => {
		if (add) {
			postSlotReleaseSchedule(data);
		} else {
			putSlotReleaseSchedule(data);
		}
		reset(data);
	};

	useEffect(() => {
		const { isError, isLoading, isSuccess, data, error } = postSlotReleaseScheduleResult;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("BOOKINGS_SCHEDULE")} ${constantTranslation.t("ADD_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ADD_ERROR")} ${settingsTranslation.t("BOOKINGS_SCHEDULE")}`, "error")));
		}
	}, [postSlotReleaseScheduleResult]);

	useEffect(() => {
		const { isError, isLoading, isSuccess, data, error } = putSlotReleaseScheduleResult;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("BOOKINGS_SCHEDULE")} ${constantTranslation.t("SAVE_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("SAVE_ERROR")} ${settingsTranslation.t("BOOKINGS_SCHEDULE")}`, "error")));
		}
	}, [putSlotReleaseScheduleResult]);

	return (
		<CustomFormDialog
			isDirty={isDirty}
			isValid={isValid}
			actions={[{ title: constantTranslation.t("SAVE"), onClick: handleSubmit((data) => submitForm(data)) }]}
			handleSubmit={handleSubmit((data) => submitForm(data))}
			title={add ? `${constantTranslation.t("ADD")} ${settingsTranslation.t("BOOKINGS_SCHEDULE")}` : `${constantTranslation.t("EDIT")} ${settingsTranslation.t("BOOKINGS_SCHEDULE")}`}
		>
			<CustomDatePickerField
				control={control}
				name="start"
				label={constantTranslation.t("ALLOWED_START") + " *"}
				errors={errors}
				slotProps={{
					textField: { InputLabelProps: { shrink: true } }
				}}
				dialog="true"
			/>
			<CustomDatePickerField
				control={control}
				name="periodStart"
				label={`${constantTranslation.t("HOLIDAY")} ${constantTranslation.t("PERIOD_START")} *`}
				errors={errors}
				slotProps={{
					textField: { InputLabelProps: { shrink: true } }
				}}
				dialog="true"
			/>
			<CustomDatePickerField
				control={control}
				name="end"
				label={constantTranslation.t("ALLOWED_END") + " *"}
				errors={errors}
				slotProps={{
					textField: { InputLabelProps: { shrink: true } }
				}}
				dialog="true"
			/>
			<CustomDatePickerField
				control={control}
				name="periodEnd"
				label={`${constantTranslation.t("HOLIDAY")} ${constantTranslation.t("PERIOD_END")} *`}
				errors={errors}
				slotProps={{
					textField: { InputLabelProps: { shrink: true } }
				}}
				dialog="true"
			/>
		</CustomFormDialog>
	);
};

export default SlotReleaseScheduleDialogForm;
