import CustomTableContainer from "app/shared-components/custom-table-container/CustomTableContainer";
import CustomTable from "app/shared-components/custom-table/CustomTable";
import { BaseSyntheticEvent, useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";
import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { highDemandHeadings } from "./constants/highDemandHeadings";
import { openDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import HighDemandDialogForm from "./high-demand-dialog-form/HighDemandDialogForm";
import { useGetAllHighDemandsQuery } from "./HighDemandApi";
import { IHighDemandEssentials, ITableHighDemand, ITableHighDemandArg, ITableHighDemandItem } from "./HighDemandModel";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import HighDemandDeleteDialogBox from "./high-demand-delete-dialog-box/HighDemandDeleteDialogBox";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import HighDemandTableActions from "./high-demand-table-actions/HighDemandTableActions";
import { DEFAULT_PAGE_SIZE } from "app/constants/DefaultConfig";
import { IGenericTable, SortOrder } from "app/constants/DefaultInterfaces";

const HighDemand = () => {
	const [fetchArgs, setFetchArgs] = useState<ITableHighDemandArg>({ businessId: 1, page: 1, pageSize: DEFAULT_PAGE_SIZE, sortBy: "startDate", sortOrder: SortOrder.ASCENDING });
	const [tableInfo, setTableInfo] = useState<IGenericTable>({ currentPage: 1, pageSize: DEFAULT_PAGE_SIZE, totalCount: 0, totalPages: 0 });
	const constantTranslation = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const dispatch = useDispatch();
	const [curatedRows, setCuratedRows] = useState<Array<ITableHighDemandItem>>([]);
	const [totalData, setTotalData] = useState(0);
	const getHighDemands = useGetAllHighDemandsQuery(fetchArgs, { refetchOnMountOrArgChange: true });

	useEffect(() => {
		const { data: rawData, isLoading, isSuccess, isError, error, isFetching } = getHighDemands;

		dispatch(showLoading(isFetching));
		dispatch(showLoading(isLoading));

		if (isSuccess && rawData) {
			const { totalCount, totalPages, currentPage, data, pageSize } = rawData;
			setTableInfo((state) => ({ ...state, totalCount, totalPages, currentPage, pageSize }));
			data.map((item) => {
				const { id, startDate, endDate, maxDaysAllowed, singleDaysAllowed } = item;
				setCuratedRows((state) => {
					const newState = state;
					newState.push({
						id,
						actions: true,
						startDate,
						endDate,
						maxDaysAllowed,
						singleDaysAllowed
					});
					return newState;
				});
			});

			setTotalData((state) => {
				if (fetchArgs.filter) {
					return state;
				} else {
					return totalCount;
				}
			});
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("HIGH_DEMAND")}`, "error")));
		}

		return () => setCuratedRows([]);
	}, [getHighDemands]);

	const handleEdit = (event: BaseSyntheticEvent) => {
		const { id } = event.target;

		dispatch(
			openDialog({
				children: (
					<HighDemandDialogForm
						add={false}
						highDemandId={id}
					/>
				)
			})
		);
	};

	const handleDelete = (event: BaseSyntheticEvent) => {
		const { id } = event.target;

		dispatch(openDialog({ children: <HighDemandDeleteDialogBox highDemandId={id} /> }));
	};

	const handlePageChange = (event: React.MouseEvent<HTMLButtonElement> | null, newPage: number) => {
		setFetchArgs((state) => ({ ...state, page: newPage + 1 }));
	};

	const handleSortChange = (event: React.MouseEvent<unknown>, property: keyof IHighDemandEssentials) => {
		setFetchArgs((state) => {
			let sortOrder;

			if (state.sortBy === property) {
				if (state.sortOrder === SortOrder.ASCENDING) {
					sortOrder = SortOrder.DESCENDING;
				} else {
					sortOrder = SortOrder.ASCENDING;
				}
			} else {
				sortOrder = SortOrder.ASCENDING;
			}

			return { ...state, sortBy: property, sortOrder };
		});
	};

	return (
		<CustomTableContainer
			title={settingsTranslation.t("HIGH_DEMAND")}
			hasAddButton={true}
			handleClick={() => dispatch(openDialog({ children: <HighDemandDialogForm add={true} /> }))}
		>
			<CustomTable
				sortBy={fetchArgs?.sortBy}
				sortOrder={fetchArgs?.sortOrder}
				showId={false}
				tableLabel={settingsTranslation.t("HIGH_DEMAND")}
				tableHeadings={highDemandHeadings()}
				tableRows={curatedRows}
				t={settingsTranslation.t}
				hasActions={true}
				tableActions={HighDemandTableActions(handleEdit, handleDelete)}
				tableInfo={tableInfo}
				handlePageChange={handlePageChange}
				handleSortChange={handleSortChange}
			/>
		</CustomTableContainer>
	);
};

export default HighDemand;
