import { apiService as api } from "app/store/apiService";
import { ISlot, ISlotEssentials, ITableSlot, ITableSlotArg } from "./SlotModel";
import moment from "moment";

export const addTagTypes = ["dailySlot/fetchAll", "dailySlot/fetchOne", "slotOverrides/fetchAll", "slotOverrides/fetchOne"] as const;

const SlotApi = api.enhanceEndpoints({ addTagTypes }).injectEndpoints({
	endpoints: (build) => ({
		// Daily slot apis
		getAllDailySlots: build.query<GetDailySlotsApiResponse, GetDailySlotsApiArg>({
			query: ({ businessId, page, pageSize, filter, businessPhaseId, shiftId, startDate, endDate }) => {
				let params = "?";

				const formatDate = (date: Date) => moment(date).format("MM/DD/YYYY");

				if (page) params += `page=${page}&`;
				if (pageSize) params += `pageSize=${pageSize}&`;
				if (filter) params += `filter=${filter}&`;
				if (businessPhaseId) params += `businessPhaseId=${businessPhaseId}&`;
				if (shiftId) params += `shiftId=${shiftId}&`;
				if (startDate) {
					let formatedDate = formatDate(startDate);
					params += `startDate=${formatedDate}&`;
				}
				if (endDate) {
					let formatedDate = formatDate(endDate);
					params += `endDate=${formatedDate}`;
				}

				return {
					url: `/business/getdailyslots/${businessId}${params}`
				};
			},
			providesTags: ["dailySlot/fetchAll"]
		}),
		// Slot overrides apis
		getAllSlotOverrides: build.query<GetSlotOverridesApiResponse, GetSlotOverridesApiArg>({
			query: ({ businessId, page, pageSize, sortBy, sortOrder, filter, businessPhaseId, shiftId, startDate, endDate }) => {
				let params = "?";

				const formatDate = (date: Date) => moment(date).format("MM/DD/YYYY");

				if (page) params += `page=${page}&`;
				if (pageSize) params += `pageSize=${pageSize}&`;
				if (sortBy) params += `sortBy=${sortBy}&`;
				if (sortOrder) params += `sortOrder=${sortOrder}&`;
				if (filter) params += `filter=${filter}&`;
				if (businessPhaseId) params += `businessPhaseId=${businessPhaseId}&`;
				if (shiftId) params += `shiftId=${shiftId}&`;
				if (startDate) {
					let formatedDate = formatDate(startDate);
					params += `startDate=${formatedDate}&`;
				}
				if (endDate) {
					let formatedDate = formatDate(endDate);
					params += `endDate=${formatedDate}`;
				}

				return {
					url: `/business/getslotoverrides/${businessId}${params}`
				};
			},
			providesTags: ["slotOverrides/fetchAll", "dailySlot/fetchAll"]
		}),
		getOneSlotOverride: build.query<GetSlotOverrideApiResponse, GetSlotOverrideApiArg>({
			query: (slotOverridesId) => ({
				url: `/business/getslotoverridesbyid/${slotOverridesId}`
			}),
			providesTags: ["slotOverrides/fetchOne"]
		}),
		postSlotOverride: build.mutation<PostSlotOverrideApiResponse, PostSlotOverrideApiArg>({
			query: (queryArg) => ({
				url: `/business/addslotoverride`,
				method: "POST",
				data: queryArg
			}),
			invalidatesTags: ["slotOverrides/fetchAll", "dailySlot/fetchAll"]
		}),
		putSlotOverride: build.mutation<PostSlotOverrideApiResponse, PostSlotOverrideApiArg>({
			query: (queryArg) => ({
				url: `/business/updateslotoverride`,
				method: "PUT",
				data: queryArg
			}),
			invalidatesTags: ["slotOverrides/fetchAll", "dailySlot/fetchAll"]
		}),
		deleteSlotOverride: build.mutation<void, DeleteSlotOverrideApiArg>({
			query: (slotOverridesId) => ({
				url: `/business/deleteslotoverride/${slotOverridesId}`,
				method: "DELETE"
			}),
			invalidatesTags: ["slotOverrides/fetchAll", "dailySlot/fetchAll"]
		})
	})
});

export default SlotApi;

// slot overrides Types
export type GetSlotOverridesApiArg = ITableSlotArg;
export type GetSlotOverridesApiResponse = ITableSlot;
export type GetSlotOverrideApiArg = string;
export type GetSlotOverrideApiResponse = ISlot;
export type PostSlotOverrideApiResponse = boolean;
export type PostSlotOverrideApiArg = ISlotEssentials;
export type PutRejectionReasonApiResponse = boolean;
export type PutRejectionReasonApiArg = ISlot;
export type DeleteSlotOverrideApiArg = string;

// daily slot Types
export type GetDailySlotsApiArg = ITableSlotArg;
export type GetDailySlotsApiResponse = ITableSlot;

export const { useGetAllDailySlotsQuery, useGetAllSlotOverridesQuery, useGetOneSlotOverrideQuery, usePostSlotOverrideMutation, usePutSlotOverrideMutation, useDeleteSlotOverrideMutation } = SlotApi;

export type SlotApiType = {
	[SlotApi.reducerPath]: ReturnType<typeof SlotApi.reducer>;
};
