import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { useGetOneHighDemandQuery, usePostHighDemandMutation, usePutHighDemandMutation } from "../HighDemandApi";
import { IHighDemand } from "../HighDemandModel";
import { z } from "zod";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import CustomFormDialog from "app/shared-components/custom-form-dialog/CustomFormDialog";
import CustomDatePickerField from "app/shared-components/custom-date-picker-field/CustomDatePickerField";
import CustomSelectField from "app/shared-components/custom-select-field/CustomSelectField";
import { selectListAvailableDays } from "../constants/selectListAvailableDays";
import CustomCheckbox from "app/shared-components/custom-checkbox/CustomCheckbox";
import moment from "moment";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";

type HighDemandDialogFormProps = {
	add?: boolean;
	highDemandId?: string;
};

const HighDemandDialogForm = (props: HighDemandDialogFormProps) => {
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const constantTranslation = useTranslation(CONSTANTS);
	const dispatch = useDispatch();
	const { add, highDemandId } = props;
	const [skip, setSkip] = useState(true);
	const [loading, setLoading] = useState(false);
	const getHighDemandById = useGetOneHighDemandQuery(highDemandId, { refetchOnMountOrArgChange: true, skip });
	const [postHighDemand, postHighDemandResult] = usePostHighDemandMutation();
	const [putHighDemand, putHighDemandResult] = usePutHighDemandMutation();

	useEffect(() => {
		if (highDemandId) {
			setSkip(false);
		}

		return () => setSkip(true);
	}, [highDemandId]);

	const defaultValues: IHighDemand = {
		id: undefined,
		businessId: 1,
		startDate: new Date(),
		endDate: new Date(),
		maxDaysAllowed: 0,
		singleDaysAllowed: false
	};

	const schema = z.object({
		id: z.number().optional(),
		businessId: z.number().optional(),
		startDate: z.date(),
		endDate: z.date(),
		maxDaysAllowed: z.number(),
		singleDaysAllowed: z.boolean()
	});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultValues,
		resolver: zodResolver(schema)
	});

	const { handleSubmit, control, formState, setValue, reset } = methods;
	const { isValid, errors, isDirty } = formState;

	useEffect(() => {
		const { data, isSuccess, isLoading, isError, error } = getHighDemandById;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			Object.keys(data).forEach((key: any) => {
				if (typeof data[key] === "string" && moment(data[key]).isValid()) {
					setValue(key, new Date(data[key]));
				} else {
					setValue(key, data[key]);
				}
			});
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("HIGH_DEMAND")}`, "error")));
		}

		return () => Object.keys(defaultValues).forEach((key: any) => setValue(key, defaultValues[key]));
	}, [getHighDemandById]);

	const submitForm = (data: IHighDemand) => {
		if (add) {
			postHighDemand(data);
		} else {
			putHighDemand(data);
		}
		reset(data);
	};

	useEffect(() => {
		const { isError, isLoading, isSuccess, data, error } = postHighDemandResult;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("HIGH_DEMAND")} ${constantTranslation.t("ADD_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ADD_ERROR")} ${settingsTranslation.t("HIGH_DEMAND")}`, "error")));
		}
	}, [postHighDemandResult]);

	useEffect(() => {
		const { isError, isLoading, isSuccess, data, error } = putHighDemandResult;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("HIGH_DEMAND")} ${constantTranslation.t("SAVE_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("SAVE_ERROR")} ${settingsTranslation.t("HIGH_DEMAND")}`, "error")));
		}
	}, [putHighDemandResult]);

	return (
		<CustomFormDialog
			isDirty={isDirty}
			isValid={isValid}
			actions={[{ title: add ? constantTranslation.t("ADD") : constantTranslation.t("SAVE"), onClick: handleSubmit((data) => submitForm(data)) }]}
			handleSubmit={handleSubmit((data) => submitForm(data))}
			title={add ? `${constantTranslation.t("ADD")} ${settingsTranslation.t("HIGH_DEMAND")} ${constantTranslation.t("PERIOD")}` : `${constantTranslation.t("EDIT")} ${settingsTranslation.t("HIGH_DEMAND")} ${constantTranslation.t("PERIOD")}`}
		>
			<CustomDatePickerField
				control={control}
				name="startDate"
				label={constantTranslation.t("PERIOD_START") + " *"}
				errors={errors}
				slotProps={{
					textField: { InputLabelProps: { shrink: true } }
				}}
				dialog="true"
			/>
			<CustomDatePickerField
				control={control}
				name="endDate"
				label={constantTranslation.t("PERIOD_END") + " *"}
				errors={errors}
				slotProps={{
					textField: { InputLabelProps: { shrink: true } }
				}}
				dialog="true"
			/>
			<CustomSelectField
				control={control}
				name="maxDaysAllowed"
				label={settingsTranslation.t("MAX_DAYS_ALLOWED")}
				errors={errors}
				selectList={selectListAvailableDays(24)}
				required
				type="number"
				InputLabelProps={{
					shrink: true
				}}
				dialog="true"
				placeholder={settingsTranslation.t("MAX_DAYS_ALLOWED")}
			/>
			<CustomCheckbox
				control={control}
				name="singleDaysAllowed"
				label={settingsTranslation.t("SINGLE_DAYS_ALLOWED")}
				errors={errors}
			/>
		</CustomFormDialog>
	);
};

export default HighDemandDialogForm;
