import CustomTextField from "app/shared-components/custom-text-field/CustomTextField";
import CustomFormDialog from "app/shared-components/custom-form-dialog/CustomFormDialog";
import { useTranslation } from "react-i18next";
import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { IBusinessPhase } from "../BusinessPhasesModel";
import { z } from "zod";
import { EMPTY_ERROR_MESSAGE } from "app/constants/FormErrors";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { useGetOneBusinessPhaseQuery, usePostBusinessPhaseMutation, usePutBusinessPhaseMutation } from "../BusinessPhasesApi";
import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";

type BusinessPhasesDialogFormProps = {
	add?: boolean;
	businessPhaseId?: string;
};

const BusinessPhasesDialogForm = (props: BusinessPhasesDialogFormProps) => {
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const constantTranslation = useTranslation(CONSTANTS);
	const dispatch = useDispatch();
	const { add, businessPhaseId } = props;
	const [skip, setSkip] = useState(true);
	const getBusinessPhaseById = useGetOneBusinessPhaseQuery(businessPhaseId, { refetchOnMountOrArgChange: true, skip });
	const [postBusinessPhase, postBusinessPhaseResult] = usePostBusinessPhaseMutation();
	const [putBusinessPhase, putBusinessPhaseResult] = usePutBusinessPhaseMutation();

	useEffect(() => {
		if (businessPhaseId) {
			setSkip(false);
		}

		return () => setSkip(true);
	}, [businessPhaseId]);

	const defaultValues: IBusinessPhase = {
		id: undefined,
		businessId: 1,
		name: "",
		numberOfLines: undefined,
		minHeadCount: undefined
	};

	const schema = z.object({
		id: z.number().optional(),
		businessId: z.number().optional(),
		name: z
			.string()
			.min(1, `${settingsTranslation.t("BUSINESS_PHASE")} ${constantTranslation.t("NAME")} ${constantTranslation.t(EMPTY_ERROR_MESSAGE)}`)
			.max(256),
		numberOfLines: z.number().min(1),
		minHeadCount: z.number().min(1)
	});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultValues,
		resolver: zodResolver(schema)
	});

	const { handleSubmit, control, formState, setValue, reset } = methods;
	const { isValid, errors, isDirty } = formState;

	useEffect(() => {
		const { data, isSuccess, isLoading, isError, error } = getBusinessPhaseById;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			Object.keys(data).forEach((key: any) => setValue(key, data[key]));
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("BUSINESS_PHASE")}`, "error")));
		}

		return () => Object.keys(defaultValues).forEach((key: any) => setValue(key, defaultValues[key]));
	}, [getBusinessPhaseById]);

	const submitForm = (data: IBusinessPhase) => {
		if (add) {
			postBusinessPhase(data);
		} else {
			putBusinessPhase(data);
		}
		reset(data);
	};

	useEffect(() => {
		const { isError, isLoading, isSuccess, data, error } = postBusinessPhaseResult;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("BUSINESS_PHASE")} ${constantTranslation.t("ADD_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ADD_ERROR")} ${settingsTranslation.t("BUSINESS_PHASE")}`, "error")));
		}
	}, [postBusinessPhaseResult]);

	useEffect(() => {
		const { isError, isLoading, isSuccess, data, error } = putBusinessPhaseResult;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("BUSINESS_PHASE")} ${constantTranslation.t("SAVE_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("SAVE_ERROR")} ${settingsTranslation.t("BUSINESS_PHASE")}`, "error")));
		}
	}, [putBusinessPhaseResult]);

	return (
		<CustomFormDialog
			isValid={isValid}
			isDirty={isDirty}
			actions={[{ title: add ? constantTranslation.t("ADD") : constantTranslation.t("SAVE"), onClick: handleSubmit((data) => submitForm(data)) }]}
			handleSubmit={handleSubmit((data) => submitForm(data))}
			title={add ? `${constantTranslation.t("ADD")} ${settingsTranslation.t("BUSINESS_PHASES")}` : `${constantTranslation.t("EDIT")} ${settingsTranslation.t("BUSINESS_PHASES")}`}
		>
			<CustomTextField
				control={control}
				name="name"
				label={constantTranslation.t("NAME")}
				errors={errors}
				required
				InputLabelProps={{
					shrink: true
				}}
				dialog="true"
				placeholder={constantTranslation.t("NAME")}
			/>
			<CustomTextField
				control={control}
				name="numberOfLines"
				label={settingsTranslation.t("NUMBER_OF_LINES")}
				errors={errors}
				required
				InputLabelProps={{
					shrink: true
				}}
				type="number"
				dialog="true"
				placeholder={settingsTranslation.t("NUMBER_OF_LINES")}
			/>
			<CustomTextField
				control={control}
				name="minHeadCount"
				label={settingsTranslation.t("MIN_HEAD_COUNT")}
				errors={errors}
				required
				InputLabelProps={{
					shrink: true
				}}
				type="number"
				dialog="true"
				placeholder={settingsTranslation.t("MIN_HEAD_COUNT")}
			/>
		</CustomFormDialog>
	);
};

export default BusinessPhasesDialogForm;
