import CustomTable from "app/shared-components/custom-table/CustomTable";
import { BaseSyntheticEvent, useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";
import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { openDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { dailySlotsHeadings } from "./constants/dailySlotsHeadings";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import CustomTableContainer from "app/shared-components/custom-table-container/CustomTableContainer";
import CustomFilterContainer from "app/shared-components/custom-filter-container/CustomFilterContainer";
import { z } from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import { ISlotFilters, ITableSlotArg, ITableSlotItem } from "../../SlotModel";
import { IGenericTable } from "app/constants/DefaultInterfaces";
import { DEFAULT_PAGE_SIZE } from "app/constants/DefaultConfig";
import { useGetAllDailySlotsQuery } from "../../SlotApi";
import DailySlotsFilterForm from "./daily-slots-filter-form/DailySlotsFilterForm";
import DailySlotsFilterDialogForm from "./daily-slots-filter-dialog-form/DailySlotsFilterDialogForm";
import moment from "moment";
import SlotOverridesDialogForm from "../../slot-overrides-dialog-form/SlotOverridesDialogForm";
import { DailySlotAddAction, DailySlotEditAction } from "./daily-slots-table-actions/DailySlotsTableActions";
import { DYNAMIC_ACTION_ID, DYNAMIC_ACTION_TRIGGER_KEY } from "./constants/dynamicActionKeys";

const DailySlots = () => {
	const [fetchArgs, setFetchArgs] = useState<ITableSlotArg>({ businessId: 1, page: 1, pageSize: DEFAULT_PAGE_SIZE });
	const [hasFilterButton, setHasFilterButton] = useState<boolean>(false);
	const constantTranslation = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const dispatch = useDispatch();
	const [curatedRows, setCuratedRows] = useState<Array<ITableSlotItem>>([]);
	const [tableInfo, setTableInfo] = useState<IGenericTable>({ currentPage: 1, pageSize: DEFAULT_PAGE_SIZE, totalCount: 0, totalPages: 0 });
	const [totalData, setTotalData] = useState(0);
	const [slotSkip, setSlotSkip] = useState(true);
	const getDailySlots = useGetAllDailySlotsQuery(fetchArgs, { refetchOnMountOrArgChange: true, skip: slotSkip });

	const defaultFilterValues: ISlotFilters = {
		businessPhaseId: null,
		shiftId: null,
		startDate: new Date(),
		endDate: new Date()
	};

	const schema = z
		.object({
			id: z.number().optional(),
			businessId: z.number().optional(),
			businessPhaseId: z.any().refine((value) => value !== null && value !== undefined, {
				message: "Phase must be selected"
			}),
			shiftId: z.any().refine((value) => value !== null && value !== undefined, {
				message: "Shift must be selected"
			}),
			startDate: z.date(),
			endDate: z.date()
		})
		.refine((data) => data.startDate <= data.endDate, {
			message: "End date must be after start date",
			path: ["endDate"]
		});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultFilterValues,
		resolver: zodResolver(schema)
	});

	const { handleSubmit, control, formState, setValue, reset, getValues } = methods;
	const { isValid, errors, isDirty } = formState;

	useEffect(() => {
		const { data: rawData, isLoading, isSuccess, isError, error, isFetching } = getDailySlots;

		dispatch(showLoading(isLoading));
		dispatch(showLoading(isFetching));

		const formatDate = (date: Date) => moment(date).format("MM/DD/YYYY");

		if (isSuccess && rawData) {
			const { totalCount, totalPages, currentPage, data, pageSize } = rawData;
			setTableInfo((state) => ({ ...state, totalCount, totalPages, currentPage, pageSize }));
			data?.map((dailySlot, index) => {
				const { id, startDate, endDate, slots, shiftName, businessPhaseName, holidayBook, businessPhaseId, shiftId, dailyDate, hasSlotOverride, slotOverrideId } = dailySlot;
				setCuratedRows((state) => {
					const newState = state;
					newState.push({
						id: index,
						actions: true,
						businessPhaseId,
						businessPhaseName,
						dailyDate: formatDate(dailyDate as Date),
						startDate: formatDate(startDate as Date),
						endDate: formatDate(endDate as Date),
						slots,
						shiftId,
						shiftName,
						holidayBook,
						hasSlotOverride,
						slotOverrideId
					});
					return newState;
				});
			});

			setTotalData((state) => {
				if (fetchArgs.filter) {
					return state;
				} else {
					return totalCount;
				}
			});
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("DAILY_SLOTS")}`, "error")));
		}

		return () => setCuratedRows([]);
	}, [getDailySlots]);

	useEffect(() => {
		setHasFilterButton(totalData > DEFAULT_PAGE_SIZE);
	}, [totalData]);

	const handlePageChange = (event: React.MouseEvent<HTMLButtonElement> | null, newPage: number) => {
		setFetchArgs((state) => ({ ...state, page: newPage + 1 }));
	};

	const handleAdd = (event: BaseSyntheticEvent) => {
		const { id } = event.target;

		const data = curatedRows.filter((row) => row.id == id);
		console.log(id, data[0]);
		dispatch(
			openDialog({
				children: (
					<SlotOverridesDialogForm
						add={true}
						slotOverrideId={data[0].slotOverrideId?.toString()}
						data={data[0]}
						fromDailySlots={true}
						dailyDate={moment(data[0].dailyDate).toDate()}
					/>
				)
			})
		);
	};
	const handleEdit = (event: BaseSyntheticEvent) => {
		const { id } = event.target;

		const data = curatedRows.filter((row) => row.id == id);

		dispatch(
			openDialog({
				children: (
					<SlotOverridesDialogForm
						add={false}
						slotOverrideId={id}
						data={data[0]}
					/>
				)
			})
		);
	};

	const submitFilter = (data) => {
		const { startDate, endDate, businessPhaseId, shiftId } = data;
		setSlotSkip(false);
		setFetchArgs((state) => ({ ...state, businessPhaseId: businessPhaseId?.value ?? 0, shiftId: shiftId?.value ?? 0, startDate: startDate, endDate: endDate }));

		// set localstorage for filter
		localStorage.setObject("dailySlots", data);
	};

	useEffect(() => {
		// check local storage for data and then filter
		const data = localStorage.getObject("dailySlots");
		if (data) {
			submitFilter(data);
		}
	}, []);

	return (
		<CustomTableContainer
			hasAddButton={false}
			hasFilterButton={true}
			handleFilterClick={() =>
				dispatch(
					openDialog({
						children: <DailySlotsFilterDialogForm submitFilter={submitFilter} />
					})
				)
			}
		>
			<CustomFilterContainer
				hasAddButton={false}
				handleFilterClick={handleSubmit((data) => submitFilter(data))}
			>
				<DailySlotsFilterForm
					control={control}
					errors={errors}
					setValue={setValue}
				/>
			</CustomFilterContainer>
			<CustomTable
				hasTabs={true}
				showId={false}
				tableLabel={settingsTranslation.t("DAILY_SLOTS")}
				tableHeadings={dailySlotsHeadings()}
				tableRows={curatedRows}
				t={settingsTranslation.t}
				hasActions={true}
				tableInfo={tableInfo}
				tableActions={DailySlotAddAction(handleAdd)}
				dynamicTableActions={DailySlotEditAction(handleEdit)}
				dynamicActionTrigger={DYNAMIC_ACTION_TRIGGER_KEY}
				dynamicActionId={DYNAMIC_ACTION_ID}
				handlePageChange={handlePageChange}
				hasFilterButton={true}
			/>
		</CustomTableContainer>
	);
};

export default DailySlots;
