import { apiService as api } from "app/store/apiService";
import { IRejectionReasons, IRejectionReasonsEssentials, ITableRejectionReasons, ITableRejectionReasonsArg } from "./RejectionReasonsModel";

export const addTagTypes = ["rejectionReason/fetchAll", "rejectionReason/fetchOne"] as const;

const RejectionReasonApi = api.enhanceEndpoints({ addTagTypes }).injectEndpoints({
	endpoints: (build) => ({
		getAllRejectionReasons: build.query<GetRejectionReasonsApiResponse, GetRejectionReasonsApiArg>({
			query: ({ businessId, page, pageSize, sortBy, sortOrder, filter }) => {
				let params = "?";

				if (page) params += `page=${page}&`;
				if (pageSize) params += `pageSize=${pageSize}&`;
				if (sortBy) params += `sortBy=${sortBy}&`;
				if (sortOrder) params += `sortOrder=${sortOrder}&`;
				if (filter) params += `filter=${filter}`;

				return {
					url: `/business/getrejectionreasons/${businessId}${params}`
				};
			},
			providesTags: ["rejectionReason/fetchAll"]
		}),
		getOneRejectionReason: build.query<GetRejectionReasonApiResponse, GetRejectionReasonApiArg>({
			query: (rejectionReasonId) => ({
				url: `/business/getrejectionreasonsbyid/${rejectionReasonId}`
			}),
			providesTags: ["rejectionReason/fetchOne"]
		}),
		postRejectionReason: build.mutation<PostRejectionReasonApiResponse, PostRejectionReasonApiArg>({
			query: (queryArg) => ({
				url: `/business/addrejectionreason`,
				method: "POST",
				data: queryArg
			}),
			invalidatesTags: ["rejectionReason/fetchAll"]
		}),
		putRejectionReason: build.mutation<PutRejectionReasonApiResponse, PutRejectionReasonApiArg>({
			query: (queryArg) => ({
				url: `/business/updaterejectionreason`,
				method: "PUT",
				data: queryArg
			}),
			invalidatesTags: ["rejectionReason/fetchAll", "rejectionReason/fetchOne"]
		}),
		deleteRejectionReason: build.mutation<void, DeleteRejectionReasonApiArg>({
			query: (rejectionReasonId) => ({
				url: `/business/deleterejectionreason/${rejectionReasonId}`,
				method: "DELETE"
			}),
			invalidatesTags: ["rejectionReason/fetchAll"]
		})
	})
});

export default RejectionReasonApi;

export type GetRejectionReasonApiArg = string;
export type GetRejectionReasonApiResponse = IRejectionReasons;
export type GetRejectionReasonsApiArg = ITableRejectionReasonsArg;
export type GetRejectionReasonsApiResponse = ITableRejectionReasons;
export type PostRejectionReasonApiResponse = boolean;
export type PostRejectionReasonApiArg = IRejectionReasonsEssentials;
export type PutRejectionReasonApiResponse = boolean;
export type PutRejectionReasonApiArg = IRejectionReasons;
export type DeleteRejectionReasonApiArg = string;

export const { useGetAllRejectionReasonsQuery, useGetOneRejectionReasonQuery, usePostRejectionReasonMutation, usePutRejectionReasonMutation, useDeleteRejectionReasonMutation } = RejectionReasonApi;

export type RejectionReasonApiType = {
	[RejectionReasonApi.reducerPath]: ReturnType<typeof RejectionReasonApi.reducer>;
};
