import CustomTable from "app/shared-components/custom-table/CustomTable";
import businessPhasesHeadings from "./constants/businessPhasesHeadings";
import CustomTableContainer from "app/shared-components/custom-table-container/CustomTableContainer";
import BusinessPhasesDeleteDialogBox from "./business-phases-delete-dialog-box/BusinessPhasesDeleteDialogBox";
import BusinessPhasesDialogForm from "./business-phases-dialog-form/BusinessPhasesDialogForm";
import BusinessPhasesTableActions from "./business-phases-table-actions/BusinessPhasesTableActions";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { openDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import { useGetAllBusinessPhasesQuery } from "./BusinessPhasesApi";
import { BaseSyntheticEvent, useEffect, useState } from "react";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { IBusinessPhaseEssentials, IBusinessPhaseFilters, ITableBusinessPhaseArg, ITableBusinessPhaseItem } from "./BusinessPhasesModel";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import { IGenericTable, SortOrder } from "app/constants/DefaultInterfaces";
import { DEFAULT_PAGE_SIZE } from "app/constants/DefaultConfig";
import CustomFilterContainer from "app/shared-components/custom-filter-container/CustomFilterContainer";
import CustomTextField from "app/shared-components/custom-text-field/CustomTextField";
import { z } from "zod";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import BusinessPhasesFilterForm from "./business-phases-filter-form/BusinessPhasesFilterForm";
import FuseSvgIcon from "@fuse/core/FuseSvgIcon";
import { Button } from "@mui/base";

const BusinessPhases = () => {
	const [fetchArgs, setFetchArgs] = useState<ITableBusinessPhaseArg>({ businessId: 1, page: 1, pageSize: DEFAULT_PAGE_SIZE, sortBy: "name", sortOrder: SortOrder.ASCENDING });
	const constantTranslation = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const dispatch = useDispatch();
	const [hasFilterButton, setHasFilterButton] = useState<boolean>(false);
	const [curatedRows, setCuratedRows] = useState<Array<ITableBusinessPhaseItem>>([]);
	const [tableInfo, setTableInfo] = useState<IGenericTable>({ currentPage: 1, pageSize: DEFAULT_PAGE_SIZE, totalCount: 0, totalPages: 0 });
	const [totalData, setTotalData] = useState(0);
	const getBusinessPhases = useGetAllBusinessPhasesQuery(fetchArgs, { refetchOnMountOrArgChange: true });

	const defaultFilterValues: IBusinessPhaseFilters = {
		name: ""
	};

	const schema = z.object({
		name: z.string().optional()
	});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultFilterValues,
		resolver: zodResolver(schema)
	});

	const { handleSubmit, control, formState, setValue } = methods;
	const { errors } = formState;

	useEffect(() => {
		const { data: rawData, isLoading, isSuccess, isError, error, isFetching } = getBusinessPhases;

		dispatch(showLoading(isLoading));
		dispatch(showLoading(isFetching));

		if (isSuccess && rawData) {
			const { totalCount, totalPages, currentPage, data, pageSize } = rawData;
			setTableInfo((state) => ({ ...state, totalCount, totalPages, currentPage, pageSize }));
			data?.map((businessPhase) => {
				const { id, name, minHeadCount, numberOfLines } = businessPhase;
				setCuratedRows((state) => {
					const newState = state;
					newState.push({
						id,
						actions: true,
						name,
						minHeadCount,
						numberOfLines
					});
					return newState;
				});
			});

			setTotalData((state) => {
				if (fetchArgs.filter) {
					return state;
				} else {
					return totalCount;
				}
			});
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("BUSINESS_PHASES")}`, "error")));
		}

		return () => setCuratedRows([]);
	}, [getBusinessPhases]);

	useEffect(() => {
		setHasFilterButton(totalData > DEFAULT_PAGE_SIZE);
	}, [totalData]);

	const handleEdit = (event: BaseSyntheticEvent) => {
		const { id } = event.target;

		dispatch(
			openDialog({
				children: (
					<BusinessPhasesDialogForm
						add={false}
						businessPhaseId={id}
					/>
				)
			})
		);
	};

	const handleDelete = (event: BaseSyntheticEvent) => {
		const { id } = event.target;

		dispatch(openDialog({ children: <BusinessPhasesDeleteDialogBox highDemandId={id} /> }));
	};

	const handlePageChange = (event: React.MouseEvent<HTMLButtonElement> | null, newPage: number) => {
		setFetchArgs((state) => ({ ...state, page: newPage + 1 }));
	};

	const handleSortChange = (event: React.MouseEvent<unknown>, property: keyof IBusinessPhaseEssentials) => {
		setFetchArgs((state) => {
			let sortOrder;

			if (state.sortBy === property) {
				if (state.sortOrder === SortOrder.ASCENDING) {
					sortOrder = SortOrder.DESCENDING;
				} else {
					sortOrder = SortOrder.ASCENDING;
				}
			} else {
				sortOrder = SortOrder.ASCENDING;
			}

			return { ...state, sortBy: property, sortOrder };
		});
	};

	return (
		<CustomTableContainer
			title={settingsTranslation.t("BUSINESS_PHASES")}
			hasAddButton={true}
			handleClick={() => dispatch(openDialog({ children: <BusinessPhasesDialogForm add={true} /> }))}
			hasFilterButton={hasFilterButton}
			handleFilterClick={() =>
				dispatch(
					openDialog({
						children: (
							<BusinessPhasesFilterForm
								control={control}
								errors={errors}
								handleSubmit={handleSubmit((data) => setFetchArgs((state) => ({ ...state, filter: data.name.length > 0 ? data.name : undefined })))}
							/>
						)
					})
				)
			}
		>
			{hasFilterButton ? (
				<CustomFilterContainer
					hasAddButton={true}
					handleFilterClick={handleSubmit((data) => setFetchArgs((state) => ({ ...state, filter: data.name.length > 0 ? data.name : undefined })))}
					handleAddClick={() => dispatch(openDialog({ children: <BusinessPhasesDialogForm add={true} /> }))}
				>
					<CustomTextField
						control={control}
						name="name"
						label={settingsTranslation.t("BUSINESS_PHASE")}
						errors={errors}
						InputLabelProps={{
							shrink: true
						}}
						InputProps={{
							endAdornment: (
								<Button onClick={() => setValue("name", "")}>
									<FuseSvgIcon
										className="text-48"
										size={24}
										color="action"
									>
										material-outline:close
									</FuseSvgIcon>
								</Button>
							)
						}}
						filter="true"
					/>
				</CustomFilterContainer>
			) : (
				""
			)}
			<CustomTable
				sortBy={fetchArgs?.sortBy}
				sortOrder={fetchArgs?.sortOrder}
				showId={false}
				tableLabel={settingsTranslation.t("BUSINESS_PHASES")}
				tableHeadings={businessPhasesHeadings()}
				tableRows={curatedRows}
				t={settingsTranslation.t}
				hasActions={true}
				tableActions={BusinessPhasesTableActions(handleEdit, handleDelete)}
				tableInfo={tableInfo}
				handlePageChange={handlePageChange}
				handleSortChange={handleSortChange}
				hasFilterButton={hasFilterButton}
			/>
		</CustomTableContainer>
	);
};

export default BusinessPhases;
