import FuseSvgIcon from '@fuse/core/FuseSvgIcon';
import { Fab } from '@mui/material';

type CustomFormButtonProps = {
	title?: string;
	[other: string]: any;
};

const CustomFormButton = (props: CustomFormButtonProps) => {
	const { title, ...other } = props;
	return (
		<Fab
			sx={{ backdropFilter: 'blur(24px)' }}
			type='submit'
			variant='extended'
			className='fixed bottom-24 right-32 md:right-64 normal-case'
			color='secondary'
			{...other}>
			<div className='flex flex-row'>
				<FuseSvgIcon
					className='text-48 mr-10'
					size={24}>
					material-solid:save
				</FuseSvgIcon>
				{title}
			</div>
		</Fab>
	);
};

export default CustomFormButton;
