import CustomTextField from "app/shared-components/custom-text-field/CustomTextField";
import CustomFormDialog from "app/shared-components/custom-form-dialog/CustomFormDialog";
import { useTranslation } from "react-i18next";
import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { z } from "zod";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import { SelectList } from "app/shared-components/custom-select-field/CustomSelectField";
import { Button } from "@mui/material";
import { useGetOneSlotOverrideQuery, usePostSlotOverrideMutation, usePutSlotOverrideMutation } from "../SlotApi";
import { ISlot, ISlotEssentials } from "../SlotModel";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import CustomDatePickerField from "app/shared-components/custom-date-picker-field/CustomDatePickerField";
import { AxiosError } from "axios";
import { ITableBusinessPhaseArg } from "../../business-phases/BusinessPhasesModel";
import { DEFAULT_PAGE_SIZE } from "app/constants/DefaultConfig";
import { SortOrder } from "app/constants/DefaultInterfaces";
import { useGetAllBusinessPhasesQuery } from "../../business-phases/BusinessPhasesApi";
import { ITableShiftsArg } from "src/app/main/pages/shifts/tabs/shifts/ShiftsModel";
import { useGetAllShiftsQuery } from "src/app/main/pages/shifts/tabs/shifts/ShiftsApi";
import CustomAutoComplete from "app/shared-components/custom-auto-complete/CustomAutoComplete";
import moment from "moment";

type SlotOverridesDialogFormProps = {
	add?: boolean;
	slotOverrideId?: string;
	data?: ISlotEssentials;
	fromDailySlots?: boolean;
	dailyDate?: Date;
};

const SlotOverridesDialogForm = (props: SlotOverridesDialogFormProps) => {
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const constantTranslation = useTranslation(CONSTANTS);
	const dispatch = useDispatch();
	const { add, slotOverrideId, data, fromDailySlots, dailyDate } = props;
	const [skip, setSkip] = useState(true);
	const getSlotOverrideById = useGetOneSlotOverrideQuery(slotOverrideId, { refetchOnMountOrArgChange: true, skip });
	const [postSlotOverride, postSlotOverrideResult] = usePostSlotOverrideMutation();
	const [putSlotOverride, putSlotOverrideResult] = usePutSlotOverrideMutation();
	const [phaseFetchArg, setPhaseFetchArg] = useState<ITableBusinessPhaseArg>({ businessId: 1, pageSize: DEFAULT_PAGE_SIZE, sortBy: "name", sortOrder: SortOrder.ASCENDING, page: 1 });
	const getBusinessPhases = useGetAllBusinessPhasesQuery(phaseFetchArg);
	const [phases, setPhases] = useState<Array<SelectList>>([]);
	const [totalPhases, setTotalPhases] = useState(0);
	const [selectedPhase, setSelectedPhase] = useState<SelectList>();

	const [shiftsFetchArg, setShiftsFetchArg] = useState<ITableShiftsArg>({ businessId: 1, pageSize: DEFAULT_PAGE_SIZE, sortBy: "name", sortOrder: SortOrder.ASCENDING, page: 1 });
	const [skipShiftsFetch, setSkipShiftsFetch] = useState(true);
	const getShifts = useGetAllShiftsQuery(shiftsFetchArg, { refetchOnMountOrArgChange: true, skip: skipShiftsFetch });
	const [shifts, setShifts] = useState<Array<SelectList>>([]);
	const [totalShifts, setTotalShifts] = useState(0);
	const [selectedShift, setSelectedShift] = useState<SelectList>();
	const [shiftIdOnEdit, setShiftIdOnEdit] = useState();

	useEffect(() => {
		if (slotOverrideId) {
			setSkip(false);
		}

		return () => setSkip(true);
	}, [slotOverrideId]);

	const defaultValues: ISlot = {
		id: undefined,
		businessId: 1,
		businessPhaseId: add ? undefined : {},
		businessPhaseName: "",
		shiftId: add ? undefined : {},
		shiftName: "",
		startDate: dailyDate || new Date(),
		endDate: dailyDate || new Date(),
		slots: null
	};

	const schema = z.object({
		id: z.number().optional(),
		businessId: z.number().optional(),
		businessPhaseId: z.any(),
		shiftId: z.any(),
		startDate: z.date().optional(),
		endDate: z.date().optional(),
		slots: z.number()
	});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultValues,
		resolver: zodResolver(schema)
	});

	const { handleSubmit, control, formState, setValue, reset } = methods;
	const { isValid, errors, isDirty } = formState;

	const submitForm = (data: ISlot) => {
		const updatedData = { ...data, businessPhaseId: selectedPhase.value, shiftId: selectedShift.value };
		if (add) {
			if (fromDailySlots) {
				const date = moment(updatedData.startDate).add("d", 1).toDate();
				postSlotOverride({ ...updatedData, startDate: date, endDate: date });
			} else {
				const startDate = moment(updatedData.startDate).add("d", 1).toDate();
				const endDate = moment(updatedData.endDate).add("d", 1).toDate();
				postSlotOverride({ ...updatedData, startDate: startDate, endDate: endDate });
			}
		} else {
			putSlotOverride(updatedData);
		}
		reset(data);
	};

	useEffect(() => {
		const { isError, isLoading, isSuccess, data, error } = postSlotOverrideResult;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("SLOT_OVERRIDES")} ${constantTranslation.t("ADD_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ADD_ERROR")} ${settingsTranslation.t("SLOT_OVERRIDES")}`, "error")));
		}
	}, [postSlotOverrideResult]);

	useEffect(() => {
		const { isError, isLoading, isSuccess, data, error } = putSlotOverrideResult;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("SLOT_OVERRIDES")} ${constantTranslation.t("SAVE_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("SAVE_ERROR")} ${settingsTranslation.t("SLOT_OVERRIDES")}`, "error")));
		}
	}, [putSlotOverrideResult]);

	useEffect(() => {
		const { isSuccess, data: rawData, isLoading, isFetching, isError, error } = getBusinessPhases;

		dispatch(showLoading(isLoading));
		dispatch(showLoading(isFetching));

		if (isSuccess && rawData) {
			const { totalCount } = rawData;

			rawData.data.map((item) => {
				if (phases.filter((phase) => phase.value == item.id).length === 0) {
					setPhases((state) => [...state, { label: item.name, value: item.id }]);
				}
			});

			setTotalPhases((state) => {
				if (phaseFetchArg.filter) {
					return state;
				} else {
					return totalCount;
				}
			});
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("BUSINESS_PHASES")}`, "error")));
		}
	}, [getBusinessPhases]);

	const onPhaseScroll = () => {
		if (phases.length < totalPhases) {
			setPhaseFetchArg((state) => {
				return { ...state, page: state.page + 1, filter: undefined };
			});
		}
	};

	const phaseInputChange = (value) => {
		if (value) {
			setTimeout(() => setPhaseFetchArg((state) => ({ ...state, filter: value })), 500);
		}
	};

	useEffect(() => {
		if (selectedPhase) {
			setSkipShiftsFetch(false);
			setShiftsFetchArg((state) => ({ ...state, businessPhaseId: selectedPhase.value as number }));
		} else if (selectedPhase === null) {
			setSkipShiftsFetch(true);
			setShifts([]);
			setSelectedShift(null);
			setValue("shiftId", undefined);
		}
		// this will clear the shift on phase change
		if (selectedPhase && selectedShift && Object.keys(selectedPhase).length && Object.keys(selectedShift).length) {
			setSelectedShift(null);
			setValue("shiftId", undefined);
		}
	}, [selectedPhase]);

	useEffect(() => {
		const { isSuccess, data: rawData, isLoading, isFetching, isError, error } = getShifts;

		dispatch(showLoading(isLoading));
		dispatch(showLoading(isFetching));

		if (isSuccess && rawData) {
			const { data, totalCount } = rawData;
			const newShiftsData = data.map((item) => ({ label: item.name, value: item.id }));
			setShifts([...newShiftsData]);

			setTotalShifts((state) => {
				if (shiftsFetchArg.filter) {
					return state;
				} else {
					return totalCount;
				}
			});
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("SHIFT")}`, "error")));
		}

		if (data && fromDailySlots) {
			setShiftIdOnEdit(data.shiftId);
		}
	}, [getShifts]);

	useEffect(() => {
		if (shifts.length > 0) {
			const shiftValue = shifts.filter((shift) => shift.value === shiftIdOnEdit)[0];
			if (shiftValue) {
				setValue("shiftId", shiftValue);
				setSelectedShift(shiftValue);
				setShiftIdOnEdit(null);
			}
		} else if (shiftIdOnEdit === null) {
			setSelectedShift(null);
		}
	}, [shifts]);

	const onShiftScroll = () => {
		if (shifts.length < totalShifts) {
			setShiftsFetchArg((state) => {
				return { ...state, page: state.page + 1, filter: undefined };
			});
		}
	};

	const shiftInputChange = (value) => {
		if (value) {
			setTimeout(() => setShiftsFetchArg((state) => ({ ...state, filter: value })), 500);
		}
	};

	useEffect(() => {
		const { data, isSuccess, isLoading, isError, error } = getSlotOverrideById;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			Object.keys(data).forEach((key: any) => {
				if (typeof data[key] === "string" && moment(data[key]).isValid()) {
					setValue(key, new Date(data[key]));
				} else {
					setValue(key, data[key]);
				}
			});
			setShiftIdOnEdit(data.shiftId);
			setValue("businessPhaseId", phases.filter((phase) => phase.value === data.businessPhaseId)[0]);
			setSelectedPhase({ label: data.businessPhaseName, value: data.businessPhaseId });
		}

		if (isError && error instanceof AxiosError) {
			if (!(error.response.status === 404 && error.response.data === "The override slots not found")) {
				dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("SLOT_OVERRIDES")}`, "error")));
			}
		}

		return () => Object.keys(defaultValues).forEach((key: any) => setValue(key, defaultValues[key]));
	}, [getSlotOverrideById]);

	useEffect(() => {
		if (data) {
			setValue("slots", data.slots);
		}
	}, [data]);

	useEffect(() => {
		if (add) {
			setSelectedPhase(phases.filter((phase) => phase.value === 1)[0]);
		}
	}, [phases]);

	return (
		<CustomFormDialog
			isValid={isValid}
			isDirty={isDirty}
			handleSubmit={handleSubmit((data) => submitForm(data))}
			title={add ? `${constantTranslation.t("ADD")} ${settingsTranslation.t("SLOT_OVERRIDE")}` : `${constantTranslation.t("EDIT")} ${settingsTranslation.t("SLOT_OVERRIDE")}`}
		>
			<div className="flex md:flex-row flex-col w-full md:gap-24">
				<div className="flex-1">
					<CustomAutoComplete
						control={control}
						name="businessPhaseId"
						label={settingsTranslation.t("PHASE")}
						errors={errors}
						selectList={phases}
						type="string"
						dialog="true"
						InputLabelProps={{
							shrink: true
						}}
						onScrollFunc={onPhaseScroll}
						onInputChange={phaseInputChange}
						onSelectState={setSelectedPhase}
						required
						defaultValue={fromDailySlots ? { label: data.businessPhaseName, value: data.businessPhaseId } : null}
					/>
				</div>
				<div className="flex-1">
					<CustomAutoComplete
						control={control}
						name="shiftId"
						label={settingsTranslation.t("SHIFT")}
						errors={errors}
						selectList={shifts}
						type="string"
						filter="true"
						InputLabelProps={{
							shrink: true
						}}
						onScrollFunc={onShiftScroll}
						onInputChange={shiftInputChange}
						onSelectState={setSelectedShift}
						selectedState={selectedShift}
						required
						defaultValue={fromDailySlots ? { label: data.shiftName, value: data.shiftId } : null}
					/>
				</div>
				<div className="flex-1">
					<CustomTextField
						control={control}
						name="slots"
						label={settingsTranslation.t("ALLOCATION")}
						errors={errors}
						required
						InputLabelProps={{
							shrink: true
						}}
						type="number"
						placeholder={settingsTranslation.t("ALLOCATION")}
						dialog="true"
					/>
				</div>
			</div>
			<div className="flex md:flex-row w-full md:gap-24 flex-col">
				<div className="flex-1">
					<CustomDatePickerField
						control={control}
						name="startDate"
						label={constantTranslation.t("DATE_START")}
						errors={errors}
						slotProps={{
							textField: { InputLabelProps: { shrink: true } }
						}}
						filter="true"
					/>
				</div>
				<div className="flex-1">
					<CustomDatePickerField
						control={control}
						name="endDate"
						label={constantTranslation.t("DATE_END")}
						errors={errors}
						slotProps={{
							textField: { InputLabelProps: { shrink: true } }
						}}
						filter="true"
						disabled={fromDailySlots}
					/>
				</div>
			</div>
			<div className="flex flex-row w-full gap-24">
				<div className="flex-1">
					<Button
						className={`rounded-md w-full`}
						color="secondary"
						variant="contained"
						disabled={!(isValid && isDirty)}
						type="submit"
					>
						{constantTranslation.t("SAVE")}
					</Button>
				</div>
			</div>
		</CustomFormDialog>
	);
};

export default SlotOverridesDialogForm;
