import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { useDeleteUserCategoryMutation } from "../UserCategoriesApi";
import { useEffect } from "react";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import CustomDialog from "app/shared-components/custom-dialog/CustomDialog";

type UserCategoriesDeleteDialogBoxProps = {
	userCategoryId: string;
};

const UserCategoriesDeleteDialogBox = (props: UserCategoriesDeleteDialogBoxProps) => {
	const constantTranslation = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);

	const { userCategoryId } = props;

	const dispatch = useDispatch();

	const [deleteUserCategory, deleteUserCategoryResult] = useDeleteUserCategoryMutation();

	useEffect(() => {
		const { isSuccess, isError, error, isLoading } = deleteUserCategoryResult;

		dispatch(showLoading(isLoading));

		if (isSuccess) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("USER_CATEGORY")} ${constantTranslation.t("DELETE_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (error && isError) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("DELETE_ERROR")} ${settingsTranslation.t("USER_CATEGORY")}`, "error")));
		}
	}, [deleteUserCategoryResult]);

	return (
		<CustomDialog
			title={`${constantTranslation.t("DELETE")} ${settingsTranslation.t("USER_CATEGORIES")}`}
			actions={[
				{
					title: constantTranslation.t("DELETE"),
					onClick: () => {
						deleteUserCategory(userCategoryId);
					}
				}
			]}
		>
			<div className="flex flex-col px-12">
				<p className="mb-10">
					{constantTranslation.t("DELETE_MSG_1")} {settingsTranslation.t("USER_CATEGORY")}?
				</p>
				<p className="mb-32">
					{constantTranslation.t("DELETE_PREFIX")} {settingsTranslation.t("USER_CATEGORY")} {constantTranslation.t("DELETE_MSG_2")}
				</p>
			</div>
		</CustomDialog>
	);
};

export default UserCategoriesDeleteDialogBox;
