import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import CustomDialog from "app/shared-components/custom-dialog/CustomDialog";
import { useTranslation } from "react-i18next";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import { useDeleteSlotOverrideMutation } from "../../../SlotApi";

type SlotOverridesDeleteDialogBoxProps = {
	slotOverrideId: string;
};

const SlotOverridesDeleteDialogBox = (props: SlotOverridesDeleteDialogBoxProps) => {
	const constantTranslation = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const { slotOverrideId } = props;
	const dispatch = useDispatch();
	const [deleteSlotOverride, deleteSlotOverrideResult] = useDeleteSlotOverrideMutation();

	useEffect(() => {
		const { isSuccess, isError, error, isLoading } = deleteSlotOverrideResult;

		dispatch(showLoading(isLoading));

		if (isSuccess) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("SLOT_OVERRIDE")}  ${constantTranslation.t("DELETE_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (error && isError) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("DELETE_ERROR")} $${settingsTranslation.t("SLOT_OVERRIDE")}`, "error")));
		}
	}, [deleteSlotOverrideResult]);

	return (
		<CustomDialog
			title={`${constantTranslation.t("DELETE")} ${settingsTranslation.t("SLOT_OVERRIDE")}`}
			actions={[
				{
					title: constantTranslation.t("DELETE"),
					onClick: () => {
						deleteSlotOverride(slotOverrideId);
					}
				}
			]}
		>
			<div className="flex flex-col px-12">
				<p className="mb-10">
					{constantTranslation.t("DELETE_MSG_1")} {settingsTranslation.t("SLOT_OVERRIDE")}?
				</p>
				<p className="mb-32">
					{constantTranslation.t("DELETE_PREFIX")} {settingsTranslation.t("SLOT_OVERRIDE")} {constantTranslation.t("DELETE_MSG_2")}
				</p>
			</div>
		</CustomDialog>
	);
};

export default SlotOverridesDeleteDialogBox;
