import { SelectList } from "app/shared-components/custom-select-field/CustomSelectField";

export const selectListAvailableDays: (days: number) => Array<SelectList> = (days) =>
	Array(days)
		.fill(0, 1)
		.map((_, index) => {
			if (index === 1) return { label: index + " day", value: index };
			return { label: index + " days", value: index };
		});
