import SectionHeader from "app/shared-components/section-header/SectionHeader";
import CustomLoadingScreen from "app/shared-components/custom-loading-screen/CustomLoadingScreen";
import CustomForm from "app/shared-components/custom-form/CustomForm";
import CustomFormButton from "app/shared-components/custom-form-button/CustomFormButton";
import BusinessForm from "./business-form/BusinessForm";
import DefaultSettingsForm from "./default-settings-form/DefaultSettingsForm";

import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";
import { useGetBusinessInfoQuery, usePutBusinessInfoMutation } from "./BusinessInfoApi";
import { FormProvider, useForm } from "react-hook-form";
import { z } from "zod";
import { zodResolver } from "@hookform/resolvers/zod";

import { BUSINESS_AREA } from "../../constants/defaultValues";
import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { EMPTY_ERROR_MESSAGE, NOT_VALID_URL } from "app/constants/FormErrors";
import { IBusinessInfo } from "./BusinessInfoModel";

import { Box, Hidden, Tab, Tabs } from "@mui/material";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
interface TabPanelProps {
	children?: React.ReactNode;
	index: number;
	tab: number;
}

const BusinessTabPanel = (props: TabPanelProps) => {
	const { children, tab, index, ...other } = props;
	return (
		<div
			role="tabpanel"
			hidden={tab !== index}
			id={`business-tabpanel-${index}`}
			aria-labelledby={`business-tab-${index}`}
			{...other}
		>
			{tab === index && <Box>{children}</Box>}
		</div>
	);
};

const BusinessInfo = () => {
	const constantTranslation = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);

	const [tab, setTab] = useState(0);

	const getBusinessInfo = useGetBusinessInfoQuery("1", { refetchOnMountOrArgChange: true });
	const [putBusinessInfo, putBusinessResult] = usePutBusinessInfoMutation();

	const dispatch = useDispatch();

	const defaultValues: IBusinessInfo = {
		businessId: undefined,
		name: "",
		logoUrl: "",
		address: "",
		registrationNumber: "",
		area: BUSINESS_AREA,
		slotsShift: undefined,
		daysBlock: undefined,
		coreHrlUrl: "",
		bookingTimeOutDays: undefined,
		sessionDurationMins: undefined,
		dayShiftStartHour: 7,
		dayShiftEndHour: 19,
		nightShiftStartHour: 19,
		nightShiftEndHour: 7,
		cancellationNoticeDays: undefined
	};

	const schema = z.object({
		businessId: z.number(),
		name: z
			.string()
			.min(1, `${settingsTranslation.t("BUSINESS_NAME")} ${constantTranslation.t(EMPTY_ERROR_MESSAGE)}`)
			.max(256),
		logoUrl: z
			.string()
			.min(1, `${settingsTranslation.t("LOGO_URL")} ${constantTranslation.t(EMPTY_ERROR_MESSAGE)}`)
			.url(constantTranslation.t(NOT_VALID_URL)),
		address: z.string().max(256),
		registrationNumber: z.string().max(256),
		area: z.string().readonly(),
		slotsShift: z.number().min(0),
		daysBlock: z.number().min(0),
		coreHrlUrl: z
			.string()
			.min(1, `${settingsTranslation.t("CORE_HR_URL")} ${constantTranslation.t(EMPTY_ERROR_MESSAGE)}`)
			.url(constantTranslation.t(NOT_VALID_URL)),
		bookingTimeOutDays: z.number().min(0),
		sessionDurationMins: z.number().min(0),
		cancellationNoticeDays: z.number().min(0),
		dayShiftStartHour: z.number().min(0).max(23),
		dayShiftEndHour: z.number().min(0).max(23),
		nightShiftStartHour: z.number().min(0).max(23),
		nightShiftEndHour: z.number().min(0).max(23)
	});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultValues,
		resolver: zodResolver(schema)
	});

	const { handleSubmit, control, formState, setValue, getValues } = methods;
	const { isValid, errors, isDirty } = formState;

	const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
		setTab(newValue);
	};

	const submitForm = (data: IBusinessInfo) => {
		putBusinessInfo(data);
	};

	useEffect(() => {
		const { data, isError, isLoading, isSuccess, error } = getBusinessInfo;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			Object.keys(data).forEach((key: any) => setValue(key, data[key]));
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("BUSINESS_INFO")}`, "error")));
		}

		return () => Object.keys(defaultValues).forEach((key: any) => setValue(key, defaultValues[key]));
	}, [getBusinessInfo]);

	useEffect(() => {
		const { isError, isLoading, isSuccess, data, error } = putBusinessResult;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("BUSINESS_INFO")} ${constantTranslation.t("SAVE_SUCCESSFUL")}`, "success")));
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("SAVE_ERROR")} ${settingsTranslation.t("BUSINESS_INFO")}`, "error")));
		}
	}, [putBusinessResult]);

	return (
		<FormProvider {...methods}>
			<CustomForm handleSubmit={handleSubmit((_data) => submitForm(_data))}>
				<Hidden mdUp>
					<Box sx={{ borderBottom: 1, borderColor: "divider", marginBottom: "1rem" }}>
						<Tabs
							value={tab}
							onChange={handleTabChange}
						>
							<Tab label={settingsTranslation.t("BUSINESS")} />
							<Tab label={settingsTranslation.t("DEFAULT_SETTINGS")} />
						</Tabs>
					</Box>
					<BusinessTabPanel
						tab={tab}
						index={0}
					>
						<BusinessForm
							control={control}
							errors={errors}
							getValues={getValues}
						/>
					</BusinessTabPanel>
					<BusinessTabPanel
						tab={tab}
						index={1}
					>
						<DefaultSettingsForm
							control={control}
							errors={errors}
						/>
					</BusinessTabPanel>
				</Hidden>
				<Hidden mdDown>
					<BusinessForm
						control={control}
						errors={errors}
						getValues={getValues}
					/>
					<SectionHeader title="Default Settings" />
					<DefaultSettingsForm
						control={control}
						errors={errors}
					/>
				</Hidden>
				<CustomFormButton
					title={constantTranslation.t("SAVE")}
					disabled={!(isValid && isDirty)}
				/>
			</CustomForm>
		</FormProvider>
	);
};

export default BusinessInfo;
