import CustomTable from "app/shared-components/custom-table/CustomTable";
import { BaseSyntheticEvent, useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";
import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { openDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { slotOverridesHeadings } from "./constants/slotOverridesHeadings";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import CustomTableContainer from "app/shared-components/custom-table-container/CustomTableContainer";
import CustomFilterContainer from "app/shared-components/custom-filter-container/CustomFilterContainer";
import { z } from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import { ISlotEssentials, ISlotFilters, ITableSlotArg, ITableSlotItem } from "../../SlotModel";
import { IGenericTable, SortOrder } from "app/constants/DefaultInterfaces";
import { useGetAllSlotOverridesQuery } from "../../SlotApi";
import { DEFAULT_PAGE_SIZE } from "app/constants/DefaultConfig";
import SlotOverridesFilterForm from "./slot-overrides-filter-form/SlotOverridesFilterForm";
import SlotOverridesFilterDialogForm from "./slot-overrides-filter-dialog-form/SlotOverridesFilterDialogForm";
import moment from "moment";
import SlotOverridesDialogForm from "../../slot-overrides-dialog-form/SlotOverridesDialogForm";
import SlotOverridesTableActions from "./slot-overrides-table-actions/SlotOverridesTableActions";
import SlotOverridesDeleteDialogBox from "./slot-overrides-delete-dialog-box/SlotOverridesDeleteDialogBox";

const SlotOverrides = () => {
	const currentDate = moment();
	const firstDateOfMonth = currentDate.startOf("month").toDate();
	const lastDateOfMonth = currentDate.endOf("month").toDate();

	const [fetchArgs, setFetchArgs] = useState<ITableSlotArg>({ businessId: 1, page: 1, pageSize: DEFAULT_PAGE_SIZE, sortBy: "phase", sortOrder: SortOrder.ASCENDING, startDate: firstDateOfMonth, endDate: lastDateOfMonth });
	const [hasFilterButton, setHasFilterButton] = useState<boolean>(false);
	const constantTranslation = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const dispatch = useDispatch();
	const [curatedRows, setCuratedRows] = useState<Array<ITableSlotItem>>([]);
	const [tableInfo, setTableInfo] = useState<IGenericTable>({ currentPage: 1, pageSize: DEFAULT_PAGE_SIZE, totalCount: 0, totalPages: 0 });
	const [totalData, setTotalData] = useState(0);
	const getSlotOverrides = useGetAllSlotOverridesQuery(fetchArgs, { refetchOnMountOrArgChange: true });

	const defaultFilterValues: ISlotFilters = {
		businessPhaseId: null,
		shiftId: null,
		startDate: firstDateOfMonth,
		endDate: lastDateOfMonth
	};

	const schema = z
		.object({
			businessPhaseId: z.any().optional(),
			shiftId: z.any().optional(),
			startDate: z.date(),
			endDate: z.date()
		})
		.refine((data) => data.startDate <= data.endDate, {
			message: "End date must be after start date",
			path: ["endDate"]
		});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultFilterValues,
		resolver: zodResolver(schema)
	});

	const { handleSubmit, control, formState, setValue, reset } = methods;
	const { isValid, errors, isDirty } = formState;

	useEffect(() => {
		const { data: rawData, isLoading, isSuccess, isError, error, isFetching } = getSlotOverrides;

		dispatch(showLoading(isLoading));
		dispatch(showLoading(isFetching));

		if (isSuccess && rawData) {
			const { totalCount, totalPages, currentPage, data, pageSize } = rawData;
			setTableInfo((state) => ({ ...state, totalCount, totalPages, currentPage, pageSize }));
			data?.map((slotOverride) => {
				const { id, startDate, endDate, slots, shiftId, businessPhaseId, businessPhaseName, shiftName } = slotOverride;
				setCuratedRows((state) => {
					const newState = state;
					newState.push({
						id,
						actions: true,
						startDate,
						endDate,
						slots,
						businessPhaseId,
						businessPhaseName,
						shiftId,
						shiftName
					});
					return newState;
				});
			});

			setTotalData((state) => {
				if (fetchArgs.filter) {
					return state;
				} else {
					return totalCount;
				}
			});
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("SLOT_OVERRIDES")}`, "error")));
		}

		return () => setCuratedRows([]);
	}, [getSlotOverrides]);

	useEffect(() => {
		setHasFilterButton(totalData > DEFAULT_PAGE_SIZE);
	}, [totalData]);

	const handlePageChange = (event: React.MouseEvent<HTMLButtonElement> | null, newPage: number) => {
		setFetchArgs((state) => ({ ...state, page: newPage + 1 }));
	};

	const handleSortChange = (event: React.MouseEvent<unknown>, property: keyof ISlotEssentials) => {
		setFetchArgs((state) => {
			let sortOrder;

			if (state.sortBy === property) {
				if (state.sortOrder === SortOrder.ASCENDING) {
					sortOrder = SortOrder.DESCENDING;
				} else {
					sortOrder = SortOrder.ASCENDING;
				}
			} else {
				sortOrder = SortOrder.ASCENDING;
			}

			return { ...state, sortBy: property, sortOrder };
		});
	};

	const submitFilter = (data) => {
		const { startDate, endDate, businessPhaseId, shiftId } = data;

		setFetchArgs((state) => ({ ...state, businessPhaseId: businessPhaseId?.value ?? 0, shiftId: shiftId?.value ?? 0, startDate: startDate, endDate: endDate }));

		// set localstorage for filter
		localStorage.setObject("slotOverrides", data);
	};

	useEffect(() => {
		// check local storage for data and then filter
		const data = localStorage.getObject("slotOverrides");
		if (data) {
			submitFilter(data);
		}
	}, []);

	const handleEdit = (event: BaseSyntheticEvent) => {
		const { id } = event.target;

		dispatch(
			openDialog({
				children: (
					<SlotOverridesDialogForm
						add={false}
						slotOverrideId={id}
						fromDailySlots={false}
					/>
				)
			})
		);
	};

	const handleDelete = (event: BaseSyntheticEvent) => {
		const { id } = event.target;

		dispatch(openDialog({ children: <SlotOverridesDeleteDialogBox slotOverrideId={id} /> }));
	};

	return (
		<CustomTableContainer
			hasAddButton={true}
			hasFilterButton={true}
			handleClick={() =>
				dispatch(
					openDialog({
						children: (
							<SlotOverridesDialogForm
								add={true}
								fromDailySlots={false}
							/>
						)
					})
				)
			}
			handleFilterClick={() =>
				dispatch(
					openDialog({
						children: <SlotOverridesFilterDialogForm submitFilter={submitFilter} />
					})
				)
			}
		>
			<CustomFilterContainer
				hasAddButton={true}
				handleFilterClick={handleSubmit((data) => submitFilter(data))}
				handleAddClick={() =>
					dispatch(
						openDialog({
							children: (
								<SlotOverridesDialogForm
									add={true}
									fromDailySlots={false}
								/>
							)
						})
					)
				}
			>
				<SlotOverridesFilterForm
					control={control}
					errors={errors}
					setValue={setValue}
				/>
			</CustomFilterContainer>

			<CustomTable
				showId={false}
				tableLabel={settingsTranslation.t("SLOT_OVERRIDES")}
				tableHeadings={slotOverridesHeadings()}
				tableRows={curatedRows}
				t={settingsTranslation.t}
				hasActions={true}
				tableInfo={tableInfo}
				handlePageChange={handlePageChange}
				handleSortChange={handleSortChange}
				hasFilterButton={hasFilterButton}
				tableActions={SlotOverridesTableActions(handleEdit, handleDelete)}
			/>
		</CustomTableContainer>
	);
};

export default SlotOverrides;
