import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import CustomDialog from "app/shared-components/custom-dialog/CustomDialog";
import { useTranslation } from "react-i18next";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import { useDeleteBusinessPhaseMutation } from "../BusinessPhasesApi";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";

type BusinessPhasesDeleteDialogBoxProps = {
	highDemandId: string;
};

const BusinessPhasesDeleteDialogBox = (props: BusinessPhasesDeleteDialogBoxProps) => {
	const constantTranslation = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const { highDemandId } = props;
	const dispatch = useDispatch();
	const [deleteBusinessPhase, deleteBusinessPhaseResult] = useDeleteBusinessPhaseMutation();

	useEffect(() => {
		const { isSuccess, isError, error, isLoading } = deleteBusinessPhaseResult;

		dispatch(showLoading(isLoading));

		if (isSuccess) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("BUSINESS_PHASES")} ${constantTranslation.t("DELETE_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (error && isError) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("DELETE_ERROR")} ${settingsTranslation.t("BUSINESS_PHASES")}`, "error")));
		}
	}, [deleteBusinessPhaseResult]);

	return (
		<CustomDialog
			title={`${constantTranslation.t("DELETE")} ${settingsTranslation.t("BUSINESS_PHASES")}`}
			actions={[
				{
					title: constantTranslation.t("DELETE"),
					onClick: () => {
						deleteBusinessPhase(highDemandId);
					}
				}
			]}
		>
			<div className="flex flex-col px-12">
				<p className="mb-10">
					{constantTranslation.t("DELETE_MSG_1")} {settingsTranslation.t("BUSINESS_PHASE")}?
				</p>
				<p className="mb-32">
					{constantTranslation.t("DELETE_PREFIX")} {settingsTranslation.t("BUSINESS_PHASE")} {constantTranslation.t("DELETE_MSG_2")}
				</p>
			</div>
		</CustomDialog>
	);
};

export default BusinessPhasesDeleteDialogBox;
