import PageHeader from 'app/shared-components/page-header/PageHeader';
import DefaultRootStyle from 'app/constants/DefaultRootStyle';
import FusePageCarded from '@fuse/core/FusePageCarded';
import SettingsContent from './SettingsContent';

import { useTranslation } from 'react-i18next';
import { styled } from '@mui/material/styles';
import { SETTINGS_PAGE } from 'app/constants/TranslationNamespaces';
import { useDispatch } from 'react-redux';
import { useEffect } from 'react';
import { setTab } from './settingsSlice';

const Root = styled(FusePageCarded)(({ theme }) => ({
	...DefaultRootStyle(theme)
}));

type SettingsProps = {
	tab?: number;
};

const Settings = (props: SettingsProps) => {
	const { tab } = props;
	const { t } = useTranslation(SETTINGS_PAGE);

	const dispatch = useDispatch();

	useEffect(() => {
		const setSettingsTab = (tab: number) => {
			dispatch(setTab({ tab }));
		};

		if (tab >= 0) {
			setSettingsTab(tab);
		}

		return () => {
			setSettingsTab(0);
		};
	}, [tab]);

	return (
		<Root
			header={<PageHeader title={t('TITLE')} />}
			scroll='page'
			content={<SettingsContent />}
		/>
	);
};

export default Settings;
