import SlotContent from "./SlotContent";
import { useEffect, useState } from "react";
import "./constants/setLocalStorage";

type SlotProps = {};

const Slot = (props: SlotProps) => {
	const [slotTab, setSlotTab] = useState(0);
	const [show, setShow] = useState(false);

	useEffect(() => {
		const localStorageKeysToClear = ["dailySlots", "slotOverrides"];

		const clearLocalStorage = () => {
			localStorageKeysToClear.forEach((key) => {
				localStorage.removeItem(key);
			});
			setShow(true);
		};

		// clear storage on mount
		clearLocalStorage();

		// clear storage on unmount
		return () => clearLocalStorage();
	}, []);

	return (
		<>
			{show && (
				<SlotContent
					slotTab={slotTab as number}
					setSlotTab={setSlotTab}
				/>
			)}
		</>
	);
};

export default Slot;
