import CustomTextField from "app/shared-components/custom-text-field/CustomTextField";
import { BaseSyntheticEvent } from "react";
import { Control, FieldErrors, FieldValues } from "react-hook-form";
import { useTranslation } from "react-i18next";
import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { useDispatch } from "react-redux";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import CustomDialog from "app/shared-components/custom-dialog/CustomDialog";

type RejectionReasonsFilterFormProps = {
	errors: FieldErrors<FieldValues>;
	control: Control;
	handleSubmit: (e?: BaseSyntheticEvent<object, any, any>) => void | {} | Promise<any>;
};

const RejectionReasonsFilterForm = (props: RejectionReasonsFilterFormProps) => {
	const dispatch = useDispatch();
	const { handleSubmit, control, errors } = props;
	const constantTranslations = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);

	return (
		<CustomDialog
			title={constantTranslations.t("FILTER") + " " + settingsTranslation.t("REJECTION_REASONS")}
			actions={[
				{
					title: constantTranslations.t("FILTER"),
					onClick: () => {
						handleSubmit();
						dispatch(closeDialog());
					}
				}
			]}
		>
			<CustomTextField
				control={control}
				name="name"
				label={settingsTranslation.t("REJECTION_REASONS")}
				errors={errors}
				InputLabelProps={{
					shrink: true
				}}
				dialog="true"
			/>
		</CustomDialog>
	);
};

export default RejectionReasonsFilterForm;
