import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import CustomDialog from "app/shared-components/custom-dialog/CustomDialog";
import { useTranslation } from "react-i18next";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import { useDeleteRejectionReasonMutation } from "../RejectionReasonsApi";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";

type RejectionReasonsDeleteDialogBoxProps = {
	rejectionReasonId: string;
};

const RejectionReasonsDeleteDialogBox = (props: RejectionReasonsDeleteDialogBoxProps) => {
	const constantTranslation = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const { rejectionReasonId } = props;
	const dispatch = useDispatch();
	const [deleteRejectionReason, deleteRejectionReasonResult] = useDeleteRejectionReasonMutation();

	useEffect(() => {
		const { isSuccess, isError, error, isLoading } = deleteRejectionReasonResult;

		dispatch(showLoading(isLoading));

		if (isSuccess) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("REJECTION_REASONS")} ${constantTranslation.t("DELETE_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (error && isError) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("DELETE_ERROR")} ${settingsTranslation.t("REJECTION_REASONS")}`, "error")));
		}
	}, [deleteRejectionReasonResult]);

	return (
		<CustomDialog
			title={`${constantTranslation.t("DELETE")} ${settingsTranslation.t("REJECTION_REASONS")}`}
			actions={[
				{
					title: constantTranslation.t("DELETE"),
					onClick: () => {
						deleteRejectionReason(rejectionReasonId);
					}
				}
			]}
		>
			<div className="flex flex-col px-12">
				<p className="mb-10">
					{constantTranslation.t("DELETE_MSG_1")} {settingsTranslation.t("REJECTION_REASONS")}?
				</p>
				<p className="mb-32">
					{constantTranslation.t("DELETE_PREFIX")} {settingsTranslation.t("REJECTION_REASONS")} {constantTranslation.t("DELETE_MSG_2")}
				</p>
			</div>
		</CustomDialog>
	);
};

export default RejectionReasonsDeleteDialogBox;
