import CustomTableContainer from "app/shared-components/custom-table-container/CustomTableContainer";
import CustomTable from "app/shared-components/custom-table/CustomTable";
import { BaseSyntheticEvent, useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";
import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { openDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { slotReleaseScheduleHeadings } from "./constants/slotReleaseScheduleHeadings";
import SlotReleaseScheduleDialogForm from "./slot-release-schedule-dialog-form/SlotReleaseScheduleDialogForm";
import { ISlotReleaseScheduleEssentials, ITableSlotReleaseScheduleArg, ITableSlotReleaseScheduleItem } from "./SlotReleaseScheduleModel";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import { DEFAULT_PAGE_SIZE } from "app/constants/DefaultConfig";
import { IGenericTable, SortOrder } from "app/constants/DefaultInterfaces";
import { useGetAllSlotReleaseSchedulesQuery } from "./SlotReleaseScheduleApi";
import SlotReleaseScheduleDeleteDialogBox from "./slot-release-schedule-delete-dialog-box/SlotReleaseScheduleDeleteDialogBox";
import SlotReleaseScheduleTableActions from "./slot-release-schedule-table-actions/SlotReleaseScheduleTableActions";

const SlotReleaseSchedule = () => {
	const [fetchArgs, setFetchArgs] = useState<ITableSlotReleaseScheduleArg>({ businessId: 1, page: 1, pageSize: DEFAULT_PAGE_SIZE, sortBy: "allowedStartDate", sortOrder: SortOrder.ASCENDING });
	const [tableInfo, setTableInfo] = useState<IGenericTable>({ currentPage: 1, pageSize: DEFAULT_PAGE_SIZE, totalCount: 0, totalPages: 0 });
	const constantTranslation = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const dispatch = useDispatch();
	const [curatedRows, setCuratedRows] = useState<Array<ITableSlotReleaseScheduleItem>>([]);
	const [totalData, setTotalData] = useState(0);
	const getSlotReleaseSchedule = useGetAllSlotReleaseSchedulesQuery(fetchArgs, { refetchOnMountOrArgChange: true });

	useEffect(() => {
		const { data: rawData, isLoading, isSuccess, isError, error, isFetching } = getSlotReleaseSchedule;

		dispatch(showLoading(isFetching));
		dispatch(showLoading(isLoading));

		if (isSuccess && rawData) {
			const { totalCount, totalPages, currentPage, data, pageSize } = rawData;
			setTableInfo((state) => ({ ...state, totalCount, totalPages, currentPage, pageSize }));
			data.map((item) => {
				const { id, start, end, periodStart, periodEnd } = item;
				setCuratedRows((state) => {
					const newState = state;
					newState.push({
						id,
						actions: true,
						start,
						end,
						periodStart,
						periodEnd
					});
					return newState;
				});
			});

			setTotalData((state) => {
				if (fetchArgs.filter) {
					return state;
				} else {
					return totalCount;
				}
			});
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("BOOKINGS_SCHEDULE")}`, "error")));
		}

		return () => setCuratedRows([]);
	}, [getSlotReleaseSchedule]);

	const handleEdit = (event: BaseSyntheticEvent) => {
		const { id } = event.target;

		dispatch(
			openDialog({
				children: (
					<SlotReleaseScheduleDialogForm
						add={false}
						slotReleaseScheduleId={id}
					/>
				)
			})
		);
	};

	const handleDelete = (event: BaseSyntheticEvent) => {
		const { id } = event.target;

		dispatch(openDialog({ children: <SlotReleaseScheduleDeleteDialogBox slotReleaseScheduleId={id} /> }));
	};

	const handlePageChange = (event: React.MouseEvent<HTMLButtonElement> | null, newPage: number) => {
		setFetchArgs((state) => ({ ...state, page: newPage + 1 }));
	};

	const handleSortChange = (event: React.MouseEvent<unknown>, property: keyof ISlotReleaseScheduleEssentials) => {
		setFetchArgs((state) => {
			let sortOrder;

			if (state.sortBy === property) {
				if (state.sortOrder === SortOrder.ASCENDING) {
					sortOrder = SortOrder.DESCENDING;
				} else {
					sortOrder = SortOrder.ASCENDING;
				}
			} else {
				sortOrder = SortOrder.ASCENDING;
			}

			return { ...state, sortBy: property, sortOrder };
		});
	};

	return (
		<CustomTableContainer
			title={settingsTranslation.t("BOOKINGS_SCHEDULE")}
			hasAddButton={true}
			handleClick={() => dispatch(openDialog({ children: <SlotReleaseScheduleDialogForm add={true} /> }))}
		>
			<CustomTable
				sortBy={fetchArgs?.sortBy}
				sortOrder={fetchArgs?.sortOrder}
				showId={false}
				tableLabel={settingsTranslation.t("BOOKINGS_SCHEDULE")}
				tableHeadings={slotReleaseScheduleHeadings()}
				tableRows={curatedRows}
				t={settingsTranslation.t}
				hasActions={true}
				tableActions={SlotReleaseScheduleTableActions(handleEdit, handleDelete)}
				tableInfo={tableInfo}
				handlePageChange={handlePageChange}
				handleSortChange={handleSortChange}
			/>
		</CustomTableContainer>
	);
};

export default SlotReleaseSchedule;
