import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { IUserCategory } from "../UserCategoriesModel";
import { z } from "zod";
import { EMPTY_ERROR_MESSAGE } from "app/constants/FormErrors";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import CustomFormDialog from "app/shared-components/custom-form-dialog/CustomFormDialog";
import CustomTextField from "app/shared-components/custom-text-field/CustomTextField";
import CustomSelectField, { SelectList } from "app/shared-components/custom-select-field/CustomSelectField";
import { useGetOneUserCategoryQuery, usePostUserCategoryMutation, usePutUserCategoryMutation } from "../UserCategoriesApi";
import { useGetAllRolesQuery } from "src/app/main/features/roles/RolesApi";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import CustomCheckbox from "app/shared-components/custom-checkbox/CustomCheckbox";

type UserCategoriesDialogFormProps = {
	add?: boolean;
	userCategoryId?: string;
};

const UserCategoriesDialogForm = (props: UserCategoriesDialogFormProps) => {
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const constantTranslation = useTranslation(CONSTANTS);
	const dispatch = useDispatch();
	const { add, userCategoryId } = props;
	const [skip, setSkip] = useState(true);
	const getUserCategoryById = useGetOneUserCategoryQuery(userCategoryId, { refetchOnMountOrArgChange: true, skip });
	const getRoles = useGetAllRolesQuery(undefined, { refetchOnMountOrArgChange: true });
	const [roles, setRoles] = useState<Array<SelectList>>([]);
	const [postUserCategory, postUsercategoryResult] = usePostUserCategoryMutation();
	const [putUserCategory, putUserCategoryResult] = usePutUserCategoryMutation();

	useEffect(() => {
		if (userCategoryId) {
			setSkip(false);
		}

		return () => setSkip(true);
	}, [userCategoryId]);

	const defaultValues: IUserCategory = {
		id: undefined,
		businessId: 1,
		category: "",
		subCategory: "",
		roleId: 1,
		roleName: "",
		maxUsers: 1,
		isMETL: false,
		isPTL: false
	};

	const schema = z.object({
		id: z.number().optional(),
		businessId: z.number().optional(),
		category: z.string().min(1, `${constantTranslation.t("CATEGORY")} ${constantTranslation.t(EMPTY_ERROR_MESSAGE)}`),
		subCategory: z.string().optional(),
		maxUsers: z.number().min(1),
		roleId: z.number(),
		roleName: z.string().optional(),
		isMETL: z.boolean(),
		isPTL: z.boolean()
	});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultValues,
		resolver: zodResolver(schema)
	});

	const { handleSubmit, control, formState, setValue, reset } = methods;
	const { isValid, errors, isDirty } = formState;

	useEffect(() => {
		const { data, isSuccess, isLoading, isError, error } = getRoles;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			data.map((role) =>
				setRoles((state) => {
					let newState = state;
					newState.push({ label: role.name, value: role.id });
					return newState;
				})
			);
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${constantTranslation.t("ROLE")}`, "error")));
		}

		return () => setRoles([]);
	}, [getRoles]);

	useEffect(() => {
		const { data, isSuccess, isLoading, isError, error } = getUserCategoryById;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			Object.keys(data).forEach((key: any) => setValue(key, data[key]));
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("USER_CATEGORY")}`, "error")));
		}

		return () => Object.keys(defaultValues).forEach((key: any) => setValue(key, defaultValues[key]));
	}, [getUserCategoryById]);

	const submitForm = (data: IUserCategory) => {
		if (add) {
			postUserCategory(data);
		} else {
			putUserCategory(data);
		}
		reset(data);
	};

	useEffect(() => {
		const { isError, isLoading, isSuccess, data, error } = postUsercategoryResult;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("USER_CATEGORY")} ${constantTranslation.t("ADD_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ADD_ERROR")} ${settingsTranslation.t("USER_CATEGORY")}`, "error")));
		}
	}, [postUsercategoryResult]);

	useEffect(() => {
		const { isError, isLoading, isSuccess, data, error } = putUserCategoryResult;

		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("USER_CATEGORY")} ${constantTranslation.t("SAVE_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("SAVE_ERROR")} ${settingsTranslation.t("USER_CATEGORY")}`, "error")));
		}
	}, [putUserCategoryResult]);

	return (
		<CustomFormDialog
			isDirty={isDirty}
			isValid={isValid}
			actions={[{ title: add ? constantTranslation.t("ADD") : constantTranslation.t("SAVE"), onClick: handleSubmit((data) => submitForm(data)) }]}
			handleSubmit={handleSubmit((data) => submitForm(data))}
			title={add ? `${constantTranslation.t("ADD")} ${settingsTranslation.t("USER_CATEGORIES")}` : `${constantTranslation.t("EDIT")} ${settingsTranslation.t("USER_CATEGORIES")}`}
		>
			<CustomTextField
				control={control}
				name="category"
				label={constantTranslation.t("NAME")}
				errors={errors}
				required
				InputLabelProps={{
					shrink: true
				}}
				dialog="true"
				placeholder={constantTranslation.t("NAME")}
			/>
			<CustomSelectField
				selectList={roles}
				control={control}
				name="roleId"
				label={constantTranslation.t("ROLE")}
				errors={errors}
				required
				type="number"
				InputLabelProps={{
					shrink: true
				}}
				dialog="true"
				placeholder={constantTranslation.t("ROLE")}
			/>
			<CustomTextField
				control={control}
				name="subCategory"
				label={constantTranslation.t("SUBCATEGORY")}
				errors={errors}
				InputLabelProps={{
					shrink: true
				}}
				dialog="true"
				placeholder={constantTranslation.t("SUBCATEGORY")}
			/>
			<CustomTextField
				control={control}
				name="maxUsers"
				label={settingsTranslation.t("MAX_USERS_PER_SHIFT")}
				errors={errors}
				required
				InputLabelProps={{
					shrink: true
				}}
				dialog="true"
				type="number"
				placeholder={settingsTranslation.t("MAX_USERS_PER_SHIFT")}
			/>
			<div className="flex flex-row w-full">
				<CustomCheckbox
					control={control}
					name="isMETL"
					label={settingsTranslation.t("IS_METL")}
					errors={errors}
				/>
				<CustomCheckbox
					control={control}
					name="isPTL"
					label={settingsTranslation.t("IS_PTL")}
					errors={errors}
				/>
			</div>
		</CustomFormDialog>
	);
};

export default UserCategoriesDialogForm;
