import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import CustomDialog from "app/shared-components/custom-dialog/CustomDialog";
import { useTranslation } from "react-i18next";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import { useDeleteSlotReleaseScheduleMutation } from "../SlotReleaseScheduleApi";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";

type SlotReleaseScheduleDeleteDialogBoxProps = {
	slotReleaseScheduleId: string;
};

const SlotReleaseScheduleDeleteDialogBox = (props: SlotReleaseScheduleDeleteDialogBoxProps) => {
	const constantTranslation = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const { slotReleaseScheduleId } = props;
	const dispatch = useDispatch();
	const [deleteSlotReleaseSchedule, deleteSlotReleaseScheduleResult] = useDeleteSlotReleaseScheduleMutation();

	useEffect(() => {
		const { isSuccess, isError, error, isLoading } = deleteSlotReleaseScheduleResult;
		dispatch(showLoading(isLoading));

		if (isSuccess) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("BOOKINGS_SCHEDULE")} ${constantTranslation.t("DELETE_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (error && isError) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("DELETE_ERROR")} ${settingsTranslation.t("BOOKINGS_SCHEDULE")}`, "error")));
		}
	}, [deleteSlotReleaseScheduleResult]);

	return (
		<CustomDialog
			title={`${constantTranslation.t("DELETE")} ${settingsTranslation.t("BOOKINGS_SCHEDULE")}`}
			actions={[
				{
					title: constantTranslation.t("DELETE"),
					onClick: () => {
						deleteSlotReleaseSchedule(slotReleaseScheduleId);
					}
				}
			]}
		>
			<div className="flex flex-col px-12">
				<p className="mb-10">
					{constantTranslation.t("DELETE_MSG_1")} {settingsTranslation.t("BOOKINGS_SCHEDULE")}?
				</p>
				<p className="mb-32">
					{constantTranslation.t("DELETE_PREFIX")} {settingsTranslation.t("BOOKINGS_SCHEDULE")} {constantTranslation.t("DELETE_MSG_2")}
				</p>
			</div>
		</CustomDialog>
	);
};

export default SlotReleaseScheduleDeleteDialogBox;
