import { apiService as api } from "app/store/apiService";
import { IHighDemand, IHighDemandEssentials, ITableHighDemand, ITableHighDemandArg } from "./HighDemandModel";

export const addTagTypes = ["highDemand/fetchAll", "highDemand/fetchOne"] as const;

const HighDemandsApi = api.enhanceEndpoints({ addTagTypes }).injectEndpoints({
	endpoints: (build) => ({
		getAllHighDemands: build.query<GetHighDemandsApiResponse, GetHighDemandsApiArg>({
			query: ({ businessId, page, pageSize, sortBy, sortOrder, filter }) => {
				let params = "?";

				if (page) params += `page=${page}&`;
				if (pageSize) params += `pageSize=${pageSize}&`;
				if (sortBy) params += `sortBy=${sortBy}&`;
				if (sortOrder) params += `sortOrder=${sortOrder}&`;
				if (filter) params += `filter=${filter}`;

				return {
					url: `/business/gethighdemandperiods/${businessId}${params}`
				};
			},
			providesTags: ["highDemand/fetchAll"]
		}),
		getOneHighDemand: build.query<GetHighDemandApiResponse, GetHighDemandApiArg>({
			query: (highDemandId) => ({
				url: `/business/gethighdemandperiodsbyid/${highDemandId}`
			}),
			providesTags: ["highDemand/fetchOne"]
		}),
		postHighDemand: build.mutation<PostHighDemandApiResponse, PostHighDemandApiArg>({
			query: (queryArg) => ({
				url: `/business/addhighdemandperiods`,
				method: "POST",
				data: queryArg
			}),
			invalidatesTags: ["highDemand/fetchAll"]
		}),
		putHighDemand: build.mutation<PutHighDemandApiResponse, PutHighDemandApiArg>({
			query: (queryArg) => ({
				url: `/business/updatehighdemandperiods`,
				method: "PUT",
				data: queryArg
			}),
			invalidatesTags: ["highDemand/fetchAll", "highDemand/fetchOne"]
		}),
		deleteHighDemand: build.mutation<void, DeleteHighDemandApiArg>({
			query: (highDemandId) => ({
				url: `/business/deletehighdemandperiods/${highDemandId}`,
				method: "DELETE"
			}),
			invalidatesTags: ["highDemand/fetchAll"]
		})
	})
});

export default HighDemandsApi;

export type GetHighDemandApiArg = string;
export type GetHighDemandApiResponse = IHighDemand;
export type GetHighDemandsApiArg = ITableHighDemandArg;
export type GetHighDemandsApiResponse = ITableHighDemand;
export type PutHighDemandApiResponse = boolean;
export type PutHighDemandApiArg = IHighDemand;
export type PostHighDemandApiResponse = boolean;
export type PostHighDemandApiArg = IHighDemandEssentials;
export type DeleteHighDemandApiArg = string;

export const { useGetAllHighDemandsQuery, useGetOneHighDemandQuery, usePostHighDemandMutation, usePutHighDemandMutation, useDeleteHighDemandMutation } = HighDemandsApi;

export type HighDemandsApiType = {
	[HighDemandsApi.reducerPath]: ReturnType<typeof HighDemandsApi.reducer>;
};
