import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import CustomTableContainer from "app/shared-components/custom-table-container/CustomTableContainer";
import CustomTable from "app/shared-components/custom-table/CustomTable";
import { BaseSyntheticEvent, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { userCategoriesHeadings } from "./constants/userCategoriesHeadings";
import { ITableUserCategoriesArg, ITableUserCategoriesItem, IUserCategoriesFilters, IUserCategory } from "./UserCategoriesModel";
import { openDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import UserCategoriesDialogForm from "./user-categories-dialog-form/UserCategoriesDialogForm";
import { IGenericTable, SortOrder } from "app/constants/DefaultInterfaces";
import { DEFAULT_PAGE_SIZE } from "app/constants/DefaultConfig";
import { useGetAllUserCategoriesQuery } from "./UserCategoriesApi";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { z } from "zod";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import CustomFilterContainer from "app/shared-components/custom-filter-container/CustomFilterContainer";
import CustomTextField from "app/shared-components/custom-text-field/CustomTextField";
import UserCategoriesDeleteDialogBox from "./user-categories-delete-dialog-box/UserCategoriesDeleteDialogBox";
import { Button } from "@mui/material";
import FuseSvgIcon from "@fuse/core/FuseSvgIcon";
import UserCategoriesTableActions from "./user-categories-table-actions/UserCategoriesTableActions";

const UserCategories = () => {
	const [fetchArgs, setFetchArgs] = useState<ITableUserCategoriesArg>({ businessId: 1, page: 1, pageSize: DEFAULT_PAGE_SIZE, sortBy: "category", sortOrder: SortOrder.ASCENDING });
	const [tableInfo, setTableInfo] = useState<IGenericTable>({ currentPage: 1, pageSize: DEFAULT_PAGE_SIZE, totalCount: 0, totalPages: 0 });
	const constantTranslation = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const dispatch = useDispatch();
	const [curatedRows, setCuratedRows] = useState<Array<ITableUserCategoriesItem>>([]);
	const [totalData, setTotalData] = useState(0);
	const [hasFilterButton, setHasFilterButton] = useState<boolean>(false);
	const getUserCategories = useGetAllUserCategoriesQuery(fetchArgs, { refetchOnMountOrArgChange: true });

	const defaultFilterValues: IUserCategoriesFilters = {
		category: ""
	};

	const schema = z.object({
		category: z.string().optional()
	});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultFilterValues,
		resolver: zodResolver(schema)
	});

	const { handleSubmit, control, formState, setValue } = methods;
	const { errors } = formState;

	useEffect(() => {
		const { data: rawData, isLoading, isSuccess, isError, error, isFetching } = getUserCategories;

		dispatch(showLoading(isLoading));
		dispatch(showLoading(isFetching));

		if (isSuccess && rawData) {
			const { totalCount, totalPages, currentPage, data, pageSize } = rawData;
			setTableInfo((state) => ({ ...state, totalCount, totalPages, currentPage, pageSize }));
			data.map((item) => {
				const { id, category, subCategory, maxUsers, roleName } = item;
				setCuratedRows((state) => {
					const newState = state;
					newState.push({
						id,
						actions: true,
						category,
						subCategory,
						maxUsers,
						roleName
					});
					return newState;
				});
			});

			setTotalData((state) => {
				if (fetchArgs.filter) {
					return state;
				} else {
					return totalCount;
				}
			});
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("USER_CATEGORIES")}`, "error")));
		}

		return () => setCuratedRows([]);
	}, [getUserCategories]);

	useEffect(() => {
		setHasFilterButton(totalData > DEFAULT_PAGE_SIZE);
	}, [totalData]);

	const handleEdit = (event: BaseSyntheticEvent) => {
		const { id } = event.target;

		dispatch(
			openDialog({
				children: (
					<UserCategoriesDialogForm
						add={false}
						userCategoryId={id}
					/>
				)
			})
		);
	};

	const handleDelete = (event: BaseSyntheticEvent) => {
		const { id } = event.target;

		dispatch(openDialog({ children: <UserCategoriesDeleteDialogBox userCategoryId={id} /> }));
	};

	const handlePageChange = (event: React.MouseEvent<HTMLButtonElement> | null, newPage: number) => {
		setFetchArgs((state) => ({ ...state, page: newPage + 1 }));
	};

	const handleSortChange = (event: React.MouseEvent<unknown>, property: keyof IUserCategory) => {
		setFetchArgs((state) => {
			let sortOrder;

			if (state.sortBy === property) {
				if (state.sortOrder === SortOrder.ASCENDING) {
					sortOrder = SortOrder.DESCENDING;
				} else {
					sortOrder = SortOrder.ASCENDING;
				}
			} else {
				sortOrder = SortOrder.ASCENDING;
			}

			return { ...state, sortBy: property, sortOrder };
		});
	};

	return (
		<CustomTableContainer
			title={settingsTranslation.t("USER_CATEGORIES")}
			hasAddButton={true}
			handleClick={() => dispatch(openDialog({ children: <UserCategoriesDialogForm add={true} /> }))}
			hasFilterButton={hasFilterButton}
		>
			{hasFilterButton ? (
				<CustomFilterContainer
					hasAddButton={true}
					handleFilterClick={handleSubmit((data) => setFetchArgs((state) => ({ ...state, filter: data.category.length > 0 ? data.category : undefined })))}
					handleAddClick={() => dispatch(openDialog({ children: <UserCategoriesDialogForm add={true} /> }))}
				>
					<CustomTextField
						control={control}
						name="category"
						label={constantTranslation.t("CATEGORY")}
						errors={errors}
						InputLabelProps={{
							shrink: true
						}}
						InputProps={{
							endAdornment: (
								<Button onClick={() => setValue("category", "")}>
									<FuseSvgIcon
										className="text-48"
										size={24}
										color="action"
									>
										material-outline:close
									</FuseSvgIcon>
								</Button>
							)
						}}
						filter="true"
					/>
				</CustomFilterContainer>
			) : (
				""
			)}
			<CustomTable
				sortBy={fetchArgs?.sortBy}
				sortOrder={fetchArgs?.sortOrder}
				showId={true}
				tableLabel={settingsTranslation.t("USER_CATEGORIES")}
				tableHeadings={userCategoriesHeadings()}
				tableRows={curatedRows}
				t={settingsTranslation.t}
				hasActions={true}
				tableActions={UserCategoriesTableActions(handleEdit, handleDelete)}
				tableInfo={tableInfo}
				handlePageChange={handlePageChange}
				handleSortChange={handleSortChange}
				hasFilterButton={hasFilterButton}
			/>
		</CustomTableContainer>
	);
};

export default UserCategories;
