import DailySlots from "./tabs/daily-slots/DailySlots";
import SlotOverrides from "./tabs/slot-overrides/SlotOverrides";
import CustomTabs from "app/shared-components/custom-tabs/CustomTabs";
import { Box } from "@mui/material";
import { SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { useTranslation } from "react-i18next";
import CustomTabPanel from "app/shared-components/custom-tab-panel/CustomTabPanel";
import slotTabItems from "./constants/slotTabItems";

type SlotContentProps = {
	slotTab: number;
	setSlotTab: React.Dispatch<React.SetStateAction<number>>;
};

const SlotContent = (props: SlotContentProps) => {
	const { slotTab, setSlotTab } = props;
	const { t } = useTranslation(SETTINGS_PAGE);

	return (
		<Box sx={{ width: "100%" }}>
			<CustomTabs
				tabValue={slotTab}
				tabLabel="slot"
				tabs={slotTabItems()}
				handleTabChange={(num) => setSlotTab(num)}
				t={t}
			/>
			<CustomTabPanel
				value={slotTab}
				index={0}
				aria="DailySlots"
			>
				<DailySlots />
			</CustomTabPanel>
			<CustomTabPanel
				value={slotTab}
				index={1}
				aria="SlotOverrides"
			>
				<SlotOverrides />
			</CustomTabPanel>
		</Box>
	);
};

export default SlotContent;
