import { SETTINGS_PAGE } from 'app/constants/TranslationNamespaces';
import { Control, FieldErrors, UseFormGetValues } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import { IBusinessInfo } from '../BusinessInfoModel';
import CustomTextField from 'app/shared-components/custom-text-field/CustomTextField';
import { useEffect, useState } from 'react';

type BusinessFormProps = {
	control: Control<IBusinessInfo, any>;
	errors: FieldErrors<IBusinessInfo>;
	getValues: UseFormGetValues<IBusinessInfo>;
};

const BusinessForm = (props: BusinessFormProps) => {
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const { control, errors, getValues } = props;
	const [logoLoaded, setLogoLoaded] = useState<boolean>(false);
	const [logoUrl, setLogoUrl] = useState<string>('');

	useEffect(() => {
		setLogoUrl(getValues('logoUrl'));
	}, []);

	return (
		<>
			<CustomTextField
				control={control}
				name='name'
				label={settingsTranslation.t('BUSINESS_NAME')}
				errors={errors}
				required
				InputLabelProps={{
					shrink: true
				}}
				placeholder={settingsTranslation.t('BUSINESS_NAME')}
			/>
			<CustomTextField
				control={control}
				name='address'
				label={settingsTranslation.t('BUSINESS_ADDRESS')}
				errors={errors}
				InputLabelProps={{
					shrink: true
				}}
				placeholder={settingsTranslation.t('BUSINESS_ADDRESS')}
			/>
			<CustomTextField
				control={control}
				name='registrationNumber'
				label={settingsTranslation.t('BUSINESS_REGISTRATION_NUMBER')}
				errors={errors}
				InputLabelProps={{
					shrink: true
				}}
				placeholder={settingsTranslation.t('BUSINESS_REGISTRATION_NUMBER')}
			/>
			<CustomTextField
				control={control}
				name='area'
				label={settingsTranslation.t('BUSINESS_AREA')}
				errors={errors}
				required
				disabled
				InputProps={{
					readOnly: true,
					shrink: true
				}}
				placeholder={settingsTranslation.t('BUSINESS_AREA')}
			/>
			<CustomTextField
				control={control}
				name='logoUrl'
				label={settingsTranslation.t('LOGO_URL')}
				errors={errors}
				required
				InputLabelProps={{
					shrink: true
				}}
				placeholder={settingsTranslation.t('LOGO_URL')}
			/>
			<div className='my-12 mx-6 w-full md:w-[45%] shrink-0'>
				{/*TODO Show Logo*/}
				<img
					width={70}
					height={70}
					src='./assets/icons/logoplaceholder.png'
					alt='business logo'
					className={`${logoLoaded ? '' : 'hidden'}`}
				/>
			</div>
		</>
	);
};

export default BusinessForm;
