import BusinessInfo from "./tabs/business-info/BusinessInfo";
import BusinessPhases from "./tabs/business-phases/BusinessPhases";
import RejectionReasons from "./tabs/rejection-reasons/RejectionReasons";
import UserCategories from "./tabs/user-categories/UserCategories";
import SlotReleaseSchedule from "./tabs/slot-release-schedule/SlotReleaseSchedule";
import Slot from "./tabs/slot/Slot";
import HighDemand from "./tabs/high-demand/HighDemand";
import CustomTabPanel from "app/shared-components/custom-tab-panel/CustomTabPanel";
import settingsTabItems from "./constants/settingsTabItems";
import CustomTabs from "app/shared-components/custom-tabs/CustomTabs";
import { Box } from "@mui/material";
import { useSelector } from "react-redux";
import { LazyLoadedSlices } from "app/store/lazyLoadedSlices";
import { SettingsState } from "./settingsSlice";
import { useTranslation } from "react-i18next";
import { SETTINGS_PAGE } from "app/constants/TranslationNamespaces";

type SettingsContentProps = {};

const SettingsContent = (props: SettingsContentProps) => {
	const { selectedTab } = useSelector<LazyLoadedSlices, SettingsState>((state) => state.settings);
	const { t } = useTranslation(SETTINGS_PAGE);

	return (
		<Box sx={{ width: "100%" }}>
			<CustomTabs
				tabValue={selectedTab}
				tabLabel="settings"
				tabs={settingsTabItems()}
				t={t}
			/>
			<CustomTabPanel
				value={selectedTab}
				index={0}
				aria="BusinessInfo"
			>
				<BusinessInfo />
			</CustomTabPanel>
			<CustomTabPanel
				value={selectedTab}
				index={1}
				aria="BusinessPhases"
			>
				<BusinessPhases />
			</CustomTabPanel>
			<CustomTabPanel
				value={selectedTab}
				index={2}
				aria="RejectionReasons"
			>
				<RejectionReasons />
			</CustomTabPanel>
			<CustomTabPanel
				value={selectedTab}
				index={3}
				aria="UserCategories"
			>
				<UserCategories />
			</CustomTabPanel>
			<CustomTabPanel
				value={selectedTab}
				index={4}
				aria="SlotReleaseSchedule"
			>
				<SlotReleaseSchedule />
			</CustomTabPanel>
			<CustomTabPanel
				value={selectedTab}
				index={5}
				aria="Slot"
			>
				<Slot />
			</CustomTabPanel>
			<CustomTabPanel
				value={selectedTab}
				index={6}
				aria="HighDemand"
			>
				<HighDemand />
			</CustomTabPanel>
		</Box>
	);
};

export default SettingsContent;
