import { apiService as api } from "app/store/apiService";
import { ISlotReleaseSchedule, ISlotReleaseScheduleEssentials, ITableSlotReleaseSchedule, ITableSlotReleaseScheduleArg } from "./SlotReleaseScheduleModel";

export const addTagTypes = ["slotReleaseSchedule/fetchAll", "slotReleaseSchedule/fetchOne"] as const;

const SlotReleaseScheduleApi = api.enhanceEndpoints({ addTagTypes }).injectEndpoints({
	endpoints: (build) => ({
		getAllSlotReleaseSchedules: build.query<GetSlotReleaseSchedulesApiResponse, GetSlotReleaseSchedulesApiArg>({
			query: ({ businessId, page, pageSize, sortBy, sortOrder, filter }) => {
				let params = "?";

				if (page) params += `page=${page}&`;
				if (pageSize) params += `pageSize=${pageSize}&`;
				if (sortBy) params += `sortBy=${sortBy}&`;
				if (sortOrder) params += `sortOrder=${sortOrder}&`;
				if (filter) params += `filter=${filter}`;

				return {
					url: `/settings/getslotreleaseschedules/${businessId}${params}`
				};
			},
			providesTags: ["slotReleaseSchedule/fetchAll"]
		}),
		getOneSlotReleaseSchedule: build.query<GetSlotReleaseScheduleApiResponse, GetSlotReleaseScheduleApiArg>({
			query: (slotReleaseScheduleId) => ({
				url: `/settings/getslotreleaseschedulesbyid/${slotReleaseScheduleId}`
			}),
			providesTags: ["slotReleaseSchedule/fetchOne"]
		}),
		postSlotReleaseSchedule: build.mutation<PostSlotReleaseScheduleApiResponse, PostSlotReleaseScheduleApiArg>({
			query: (queryArg) => ({
				url: `/settings/addslotreleaseschedules`,
				method: "POST",
				data: queryArg
			}),
			invalidatesTags: ["slotReleaseSchedule/fetchAll"]
		}),
		putSlotReleaseSchedule: build.mutation<PutSlotReleaseScheduleApiResponse, PutSlotReleaseScheduleApiArg>({
			query: (queryArg) => ({
				url: `/settings/updateslotreleaseschedules`,
				method: "PUT",
				data: queryArg
			}),
			invalidatesTags: ["slotReleaseSchedule/fetchAll", "slotReleaseSchedule/fetchOne"]
		}),
		deleteSlotReleaseSchedule: build.mutation<void, DeleteSlotReleaseScheduleApiArg>({
			query: (slotReleaseScheduleId) => ({
				url: `/settings/deleteslotreleaseschedules/${slotReleaseScheduleId}`,
				method: "DELETE"
			}),
			invalidatesTags: ["slotReleaseSchedule/fetchAll"]
		})
	})
});

export default SlotReleaseScheduleApi;

export type GetSlotReleaseScheduleApiArg = string;
export type GetSlotReleaseScheduleApiResponse = ISlotReleaseSchedule;
export type GetSlotReleaseSchedulesApiArg = ITableSlotReleaseScheduleArg;
export type GetSlotReleaseSchedulesApiResponse = ITableSlotReleaseSchedule;
export type PutSlotReleaseScheduleApiResponse = boolean;
export type PutSlotReleaseScheduleApiArg = ISlotReleaseSchedule;
export type PostSlotReleaseScheduleApiResponse = boolean;
export type PostSlotReleaseScheduleApiArg = ISlotReleaseScheduleEssentials;
export type DeleteSlotReleaseScheduleApiArg = string;

export const { useGetAllSlotReleaseSchedulesQuery, useGetOneSlotReleaseScheduleQuery, usePostSlotReleaseScheduleMutation, usePutSlotReleaseScheduleMutation, useDeleteSlotReleaseScheduleMutation } = SlotReleaseScheduleApi;

export type SlotReleaseScheduleApiType = {
	[SlotReleaseScheduleApi.reducerPath]: ReturnType<typeof SlotReleaseScheduleApi.reducer>;
};
