import CustomTable from "app/shared-components/custom-table/CustomTable";
import CustomTableContainer from "app/shared-components/custom-table-container/CustomTableContainer";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { openDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import { BaseSyntheticEvent, useEffect, useState } from "react";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import RejectionReasonsDialogForm from "./rejection-reasons-dialog-form/RejectionReasonsDialogForm";
import rejectionReasonsHeadings from "./constants/rejectionReasonsHeadings";
import { useGetAllRejectionReasonsQuery } from "./RejectionReasonsApi";
import { IRejectionReasonsEssentials, IRejectionReasonsFilters, ITableRejectionReasons, ITableRejectionReasonsArg, ITableRejectionReasonsItem } from "./RejectionReasonsModel";
import RejectionReasonsDeleteDialogBox from "./rejection-reasons-delete-dialog-box/RejectionReasonsDeleteDialogBox";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import RejectionReasonsTableActions from "./rejection-reasons-table-actions/RejectionReasonsTableActions";
import { DEFAULT_PAGE_SIZE } from "app/constants/DefaultConfig";
import { IGenericTable, SortOrder } from "app/constants/DefaultInterfaces";
import { z } from "zod";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import CustomFilterContainer from "app/shared-components/custom-filter-container/CustomFilterContainer";
import CustomTextField from "app/shared-components/custom-text-field/CustomTextField";
import RejectionReasonsFilterForm from "./rejection-reasons-filter-form/RejectionReasonsFilterForm";

const RejectionReasons = () => {
	const [fetchArgs, setFetchArgs] = useState<ITableRejectionReasonsArg>({ businessId: 1, page: 1, pageSize: DEFAULT_PAGE_SIZE, sortBy: "name", sortOrder: SortOrder.ASCENDING });
	const constantTranslation = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const dispatch = useDispatch();
	const [curatedRows, setCuratedRows] = useState<Array<ITableRejectionReasonsItem>>([]);
	const [hasFilterButton, setHasFilterButton] = useState<boolean>(false);
	const [tableInfo, setTableInfo] = useState<IGenericTable>({ currentPage: 1, pageSize: DEFAULT_PAGE_SIZE, totalCount: 0, totalPages: 0 });
	const [totalData, setTotalData] = useState(0);
	const getRejectionReasons = useGetAllRejectionReasonsQuery(fetchArgs, { refetchOnMountOrArgChange: true });

	const defaultFilterValues: IRejectionReasonsFilters = {
		name: ""
	};

	const schema = z.object({
		name: z.string().optional()
	});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultFilterValues,
		resolver: zodResolver(schema)
	});

	const { handleSubmit, control, formState } = methods;
	const { errors } = formState;

	useEffect(() => {
		const { data: rawData, isLoading, isSuccess, isError, error, isFetching } = getRejectionReasons;

		dispatch(showLoading(isLoading));
		dispatch(showLoading(isFetching));

		if (isSuccess && rawData) {
			const { totalCount, totalPages, currentPage, data, pageSize } = rawData;
			setTableInfo((state) => ({ ...state, totalCount, totalPages, currentPage, pageSize }));
			data?.map((rejectionReasons) => {
				const { id, name } = rejectionReasons;
				setCuratedRows((state) => {
					const newState = state;
					newState.push({
						id,
						actions: true,
						name
					});
					return newState;
				});
			});

			setTotalData((state) => {
				if (fetchArgs.filter) {
					return state;
				} else {
					return totalCount;
				}
			});
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${settingsTranslation.t("BUSINESS_PHASES")}`, "error")));
		}

		return () => setCuratedRows([]);
	}, [getRejectionReasons]);

	useEffect(() => {
		setHasFilterButton(totalData > DEFAULT_PAGE_SIZE);
	}, [totalData]);

	const handleEdit = (event: BaseSyntheticEvent) => {
		const { id } = event.target;

		dispatch(
			openDialog({
				children: (
					<RejectionReasonsDialogForm
						add={false}
						rejectionReasonId={id}
					/>
				)
			})
		);
	};

	const handleDelete = (event: BaseSyntheticEvent) => {
		const { id } = event.target;

		dispatch(openDialog({ children: <RejectionReasonsDeleteDialogBox rejectionReasonId={id} /> }));
	};

	const handlePageChange = (event: React.MouseEvent<HTMLButtonElement> | null, newPage: number) => {
		setFetchArgs((state) => ({ ...state, page: newPage + 1 }));
	};

	const handleSortChange = (event: React.MouseEvent<unknown>, property: keyof IRejectionReasonsEssentials) => {
		setFetchArgs((state) => {
			let sortOrder;

			if (state.sortBy === property) {
				if (state.sortOrder === SortOrder.ASCENDING) {
					sortOrder = SortOrder.DESCENDING;
				} else {
					sortOrder = SortOrder.ASCENDING;
				}
			} else {
				sortOrder = SortOrder.ASCENDING;
			}

			return { ...state, sortBy: property, sortOrder };
		});
	};

	return (
		<CustomTableContainer
			title={settingsTranslation.t("REJECTION_REASONS")}
			hasAddButton={true}
			handleClick={() => dispatch(openDialog({ children: <RejectionReasonsDialogForm add={true} /> }))}
			hasFilterButton={hasFilterButton}
			handleFilterClick={() =>
				dispatch(
					openDialog({
						children: (
							<RejectionReasonsFilterForm
								control={control}
								errors={errors}
								handleSubmit={handleSubmit((data) => setFetchArgs((state) => ({ ...state, filter: data.name.length > 0 ? data.name : undefined })))}
							/>
						)
					})
				)
			}
		>
			{hasFilterButton ? (
				<CustomFilterContainer
					hasAddButton={true}
					handleFilterClick={handleSubmit((data) => setFetchArgs((state) => ({ ...state, filter: data.name.length > 0 ? data.name : undefined })))}
					handleAddClick={() => dispatch(openDialog({ children: <RejectionReasonsDialogForm add={true} /> }))}
				>
					<CustomTextField
						control={control}
						name="name"
						label={settingsTranslation.t("REJECTION_REASONS")}
						errors={errors}
						InputLabelProps={{
							shrink: true
						}}
						filter="true"
					/>
				</CustomFilterContainer>
			) : (
				""
			)}
			<CustomTable
				sortBy={fetchArgs?.sortBy}
				sortOrder={fetchArgs?.sortOrder}
				showId={false}
				tableLabel={settingsTranslation.t("REJECTION_REASONS")}
				tableHeadings={rejectionReasonsHeadings()}
				tableRows={curatedRows}
				t={settingsTranslation.t}
				hasActions={true}
				tableActions={RejectionReasonsTableActions(handleEdit, handleDelete)}
				tableInfo={tableInfo}
				handlePageChange={handlePageChange}
				handleSortChange={handleSortChange}
				hasFilterButton={hasFilterButton}
			/>
		</CustomTableContainer>
	);
};

export default RejectionReasons;
