import { BaseSyntheticEvent } from "react";
import { Control, FieldErrors, FieldValues, UseFormSetValue, useForm } from "react-hook-form";
import { useTranslation } from "react-i18next";
import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import { useDispatch } from "react-redux";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import CustomDialog from "app/shared-components/custom-dialog/CustomDialog";
import SlotOverridesFilterForm from "../slot-overrides-filter-form/SlotOverridesFilterForm";
import { ISlotFilters } from "../../../SlotModel";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import moment from "moment";

type SlotOverridesFilterDialogFormProps = {
	submitFilter: (data) => void;
};

const SlotOverridesFilterDialogForm = (props: SlotOverridesFilterDialogFormProps) => {
	const currentDate = moment();
	const firstDateOfMonth = currentDate.startOf("month").toDate();
	const lastDateOfMonth = currentDate.endOf("month").toDate();

	const dispatch = useDispatch();
	const { submitFilter } = props;
	const constantTranslations = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);

	const defaultFilterValues: ISlotFilters = {
		businessPhaseId: null,
		shiftId: null,
		startDate: firstDateOfMonth,
		endDate: lastDateOfMonth
	};

	const schema = z
		.object({
			businessPhaseId: z.any().optional(),
			shiftId: z.any().optional(),
			startDate: z.date(),
			endDate: z.date()
		})
		.refine((data) => data.startDate <= data.endDate, {
			message: "End date must be after start date",
			path: ["endDate"]
		});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultFilterValues,
		resolver: zodResolver(schema)
	});

	const { handleSubmit, control, formState, setValue, reset, getValues } = methods;
	const { isValid, errors, isDirty } = formState;

	return (
		<CustomDialog
			title={constantTranslations.t("FILTER") + " " + settingsTranslation.t("SLOT_OVERRIDES")}
			isDirty={isDirty}
			isValid={isValid}
			isForm={true}
			actions={[
				{
					title: constantTranslations.t("FILTER"),
					onClick: () => {
						const data = getValues();
						submitFilter(data);
						dispatch(closeDialog());
					}
				}
			]}
		>
			<SlotOverridesFilterForm
				control={control}
				errors={errors}
				setValue={setValue}
			/>
		</CustomDialog>
	);
};

export default SlotOverridesFilterDialogForm;
