import { CONSTANTS, SETTINGS_PAGE } from "app/constants/TranslationNamespaces";
import CustomDialog from "app/shared-components/custom-dialog/CustomDialog";
import { useTranslation } from "react-i18next";
import { useDeleteHighDemandMutation } from "../HighDemandApi";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";

type HighDemandDeleteDialogBoxProps = {
	highDemandId: string;
};

const HighDemandDeleteDialogBox = (props: HighDemandDeleteDialogBoxProps) => {
	const constantTranslation = useTranslation(CONSTANTS);
	const settingsTranslation = useTranslation(SETTINGS_PAGE);
	const { highDemandId } = props;
	const dispatch = useDispatch();
	const [deleteHighDemandPeriod, deleteHighDemandPeriodResult] = useDeleteHighDemandMutation();

	useEffect(() => {
		const { isSuccess, isError, error, isLoading } = deleteHighDemandPeriodResult;

		dispatch(showLoading(isLoading));

		if (isSuccess) {
			setTimeout(() => {
				dispatch(showMessage(snackbarDefaults(`${settingsTranslation.t("HIGH_DEMAND")} ${constantTranslation.t("PERIOD")} ${constantTranslation.t("DELETE_SUCCESSFUL")}`, "success")));
			}, 250);
			dispatch(closeDialog());
		}

		if (error && isError) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("DELETE_ERROR")} ${settingsTranslation.t("HIGH_DEMAND")} ${constantTranslation.t("PERIOD")}`, "error")));
		}
	}, [deleteHighDemandPeriodResult]);

	return (
		<CustomDialog
			title={`${constantTranslation.t("DELETE")} ${settingsTranslation.t("HIGH_DEMAND")}`}
			actions={[
				{
					title: constantTranslation.t("DELETE"),
					onClick: () => {
						deleteHighDemandPeriod(highDemandId);
					}
				}
			]}
		>
			<div className="flex flex-col px-12">
				<p className="mb-10">
					{constantTranslation.t("DELETE_MSG_1")} {settingsTranslation.t("HIGH_DEMAND")} {constantTranslation.t("PERIOD")}?
				</p>
				<p className="mb-32">
					{constantTranslation.t("DELETE_PREFIX")} {settingsTranslation.t("HIGH_DEMAND")} {constantTranslation.t("PERIOD")} {constantTranslation.t("DELETE_MSG_2")}
				</p>
			</div>
		</CustomDialog>
	);
};

export default HighDemandDeleteDialogBox;
